// 高拍仪设备 开始
const addEvent = (obj, name, func) => {
    if (obj.attachEvent) {
        obj.attachEvent(name, func);
    } else {
        obj.addEventListener(name, func, false);
    }
};

export const unloadVideoDevice = (el) => {
    try {
        const element = el || document.getElementById('view1');
        closeVideoDevice();
        if (DeviceMain) {
            element.Device_Release(DeviceMain);
            DeviceMain = '';
            console.log('高拍仪设备已关闭');
        }
        element.Global_DeinitDevs();
    } catch (error) {
        // console.log('关闭高拍仪出现异常', JSON.stringify(error));
    }
};

export const closeVideoDevice = (el) => {
    try {
        const element = el || document.getElementById('view1');
        if (VideoMain) {
            element.View_SetText('', 0);
            element.Video_Release(VideoMain);
            VideoMain = null;
            console.log('高拍仪视频已关闭');
        }
    } catch (error) {
        // console.log('关闭高拍仪出现异常', JSON.stringify(error));
    }
};

export const openVideoMain = (el) => {
    try {
        const element = el || document.getElementById('view1');
        closeVideoDevice(element);
        VideoMain = element.Device_CreateVideo(DeviceMain, DeviceResolution, DeviceType); // 创建视频
        if (VideoMain) {
            console.log('创建视频成功', VideoMain);
            element.View_SetText('打开视频中，请勿操作...', 0);
            element.View_SelectVideo(VideoMain);
            return {
                message: '打开视频成功',
                code: 0
            };
        } else {
            return {
                message: '创建视频失败',
                code: -1
            };
        }
    } catch (error) {
        console.log('打开高拍仪出现异常', error, JSON.stringify(error));
    }
};

export const initDeviceMain = (el) => {
    const element = el || document.getElementById('view1');
    addEvent(element, 'onDevChange', function (type, idx, dbt) {
        console.log('初始化视频触发监听', type, idx, dbt);
        if (type === 1) { // 视频设备
            if (dbt === 1) { // 设备到达
                console.log('设备到达');
                setTimeout(() => {
                    DeviceMain = element.Global_CreateDevice(1, idx); // 创建设备
                    if (DeviceMain) {
                        console.log('创建设备成功');
                        DeviceType = element.Device_GetSubtype(DeviceMain);
                        if (DeviceType !== 0) {
                            DeviceType = 2;
                        }
                        openVideoMain();
                    }
                }, 2000);
            } else if (dbt === 2) { // 设备丢失
                unloadVideoDevice();
                console.log('设备丢失');
            }
        }
    });
    try {
        element.Global_SetWindowName('view');
        element.Global_InitDevs();
    } catch (error) {
        console.log('初始化设备出现异常', error);
    }
};

