// 双目摄像头 开始
export const openFaceDevice = (el) => {
    try {
        const element = el || document.getElementById('stdfcectl');
        let result = element.openDevice(szParam);
        result = XMLResolve(result);
        result = xmlToJson(result);
        if (result.returnMsg) {
            result.code = result.returnMsg.resultCode['#text'];
            result.message = result.returnMsg.resultMsg['#text'];
        }
        console.log('双目摄像头打开：', JSON.stringify(result));
        return result;
    } catch (error) {
        console.log('双目摄像头打开异常：', JSON.stringify(error));
        return error;
    }
};

export const autoFaceDevice = (el, getImgBase64Src) => {
    try {
        if (getImgBase64Src) {
            document.getElementById('stdfcectl').onGetImg = getImgBase64Src;
        }
        const element = el || document.getElementById('stdfcectl');
        let result = element.getFaceB64A(szIdCard, szSerise, szParam);
        result = XMLResolve(result);
        result = xmlToJson(result);
        if (result.returnMsg) {
            result.code = result.returnMsg.resultCode['#text'];
            result.message = result.returnMsg.resultMsg['#text'];
        }
        console.log('双目摄像头自动拍照：', JSON.stringify(result));
        return result;
    } catch (error) {
        console.log('双目摄像头自动拍照异常：', JSON.stringify(error));
        return error;
    }
};

export const getBase64Img = (el) => {
    try {
        const element = el || document.getElementById('stdfcectl');
        const resString = element.GetImageData();
        // setTimeout(() => {
        // 	showBase64Img(element, resString);
        // }, 1000);
        let result = XMLResolve(resString);
        result = xmlToJson(result);
        if (result.returnMsg) {
            result.code = result.returnMsg.resultCode['#text'];
            result.message = result.returnMsg.resultMsg['#text'];
            result.imgBase64 = addPrefix(result.returnMsg.imgBase['#text']);
        }
        console.log('双目摄像头获取到照片：', JSON.stringify(result));
        return result;
    } catch (error) {
        console.log('双目摄像头获取到照片异常：', JSON.stringify(error));
        return error;
    }
};

