const XMLResolve = (xmlStr) => {
    var parseXML = '';

    if (typeof DOMParser === 'function') {
        parseXML = function (xmlStr) {
            return (new DOMParser()).parseFromString(xmlStr, 'text/xml');
        };
    } else if (typeof window.ActiveXObject !== 'undefined' && new Window.ActiveXObject('Microsoft.XMLDOM')) {
        parseXML = function (xmlStr) {
            var xmlDOC = new Window.ActiveXObject('Microsoft.XMLDOM');
            xmlDOC.async = 'false';
            xmlDOC.loadXML(xmlStr);
            return xmlDOC;
        };
    } else {
        throw new Error('No XML parser found');
    }
    return parseXML(xmlStr);
};

const xmlToJson = (xml) => {
    // Create the return object
    var obj = {};

    if (xml.nodeType === 1) { // element
        // do attributes
        if (xml.attributes.length > 0) {
            obj['@attributes'] = {};
            for (var j = 0; j < xml.attributes.length; j++) {
                var attribute = xml.attributes.item(j);
                obj['@attributes'][attribute.nodeName] = attribute.nodeValue;
            }
        }
    } else if (xml.nodeType === 3) { // text
        obj = xml.nodeValue;
    }

    // do children
    if (xml.hasChildNodes()) {
        for (var i = 0; i < xml.childNodes.length; i++) {
            var item = xml.childNodes.item(i);
            var nodeName = item.nodeName;
            if (typeof (obj[nodeName]) === 'undefined') {
                obj[nodeName] = xmlToJson(item);
            } else {
                if (typeof (obj[nodeName].length) === 'undefined') {
                    var old = obj[nodeName];
                    obj[nodeName] = [];
                    obj[nodeName].push(old);
                }
                obj[nodeName].push(xmlToJson(item));
            }
        }
    }
    return obj;
};

// 签注模块
export const handleEndorsement = (funName, data = '') => {
    // 获取OCX版本信息:GetOCXVersion
    // 启动签注模块:StartMachine
    // 进卡，方向识别，翻转:FeedCard
    // 读芯片信息:ReadCardInfo
    // 签注(写芯片，打印，质检):VisaCard
    // 退卡:EjectCard

    // 登录受理接口:LoginZZ
    // 获取证件状态:GetZJStatus:{"ZJHM":"","ZJZL":""}
    // 获取申请信息:GetApplyInfo:{"ZJHM":"","ZJZL":""}
    // 检查控制对象:Checkkzdx:{"SFZH":"","HKSZD":""}
    // 获取业务编号:GetYWBH
    // 保存申请信息:SaveApplyInfo:{"DataFile":""}申请信息XML的路径
    // 自动审批签注:AutoSPQZ:{"YWBH":"","SJSFJE":"","JFFS":"","YSLSH":"","YHKH":""}
    // 签注信息推送到制证库:SendPrintQZ:{"YWBH":""}
    // 接受制证库签注:ReceiveQZ:{"YWBH":""}

    // 业务编号:YWBH
    // 实际收费金额:SJSFJE
    // 缴费方式:JFFS
    // 银行流水号:YSLSH
    // 银行卡号:YHKH
    return new Promise((resolve) => {
        try {
            const endorsement = document.getElementById('Client');
            if (funName && endorsement) {
                let result = endorsement.allfunc(funName, data);
                if (result) {
                    result = JSON.parse(result);
                    if (result.returninfo) {
                        result.message = result.returninfo;
                    }
                    if (result.success === '1') {
                        result.code = '0';
                    } else {
                        result.code = '-1';
                    }
                    resolve(result);
                } else {
                    resolve({ code: '-1' });
                }
            } else {
                resolve({ code: '-1' });
            }
        } catch (e) {
            resolve(e);
        }
    });
};
