import { cloneDeep, assign } from 'lodash';

const defaultState = {
    certNumber: '',
    partyName: '',
    identityPic: '',
    binocular: '',
    relationship: '3',
    carType: '02',
    carLicense: '',
    result: {}
};
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        setUserInfo (state, payload) {
            Object.keys(payload).forEach((item) => {
                state[item] = payload[item];
            });
        },
        setCarInfo (state, payload) {
            state.carType = payload.carType;
            state.carLicense = payload.carLicense;
        },
        setRelationship (state, payload) {
            state.relationship = payload;
        },
        setResult (state, payload) {
            state.result = payload;
        },
        initData (state) {
            assign(state, cloneDeep(defaultState));
        }

    }
};
