import { cloneDeep, assign } from 'lodash';
import img1 from '@/assets/demo/0001.png';
import img2 from '@/assets/demo/0002.png';
import img3 from '@/assets/demo/0003.png';

const defaultState = {
    businessType: 0,
    businessImg: null,
    cardInfo: {}
};
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        setBusinessType (state, payload) {
            state.businessType = payload;
            if (payload === 1) {
                state.businessImg = img1;
            } else if (payload === 2) {
                state.businessImg = img2;
            } else if (payload === 3) {
                state.businessImg = img3;
            }
        },
        setCardInfo (state, payload) {
            state.cardInfo = payload;
        },
        initData (state) {
            assign(state, cloneDeep(defaultState));
        }
    }
};
