<template>
    <div>
        <ul class="steps_warp">
            <template v-for="(item, index) in stepsItem">
                <li class="block" :class="{active:item.isActive}" :key="index" v-if="item.visible">
                    <div class="top"></div>
                    <div class="bottom"></div>
                    <div class="text">{{ item.name }}</div>
                </li>
            </template>
        </ul>
    </div>
</template>

<script>
export default {
    name: 'StepHead',
    props: {
        stepsItem: {
            type: Array,
            default: function () {
                return [];
            }
        }
    },
    data () {
        return {
            stepVisibleNum: 0
        };
    },
    mounted () {
        this.getStepNum();
    },
    watch: {
        stepsItem (newVal) {
            this.stepVisibleNum = 0;
            this.getStepNum();
        }
    },
    methods: {
        getStepNum () {
            for (const i of this.stepsItem) {
                if (i.visible) {
                    this.stepVisibleNum++;
                }
            }
        }
    }
};
</script>

<style lang="scss" scoped>

    .steps_warp {
        width: 100%;
        display: flex;

        li {
            list-style: none;
            text-align: center;
            height: 70px;
            line-height: 70px;
            font-size: 20px;
            color: #FFFFFF;
            flex: 1;
        }

        @mixin liner($color1:#2F71B5,$color2:#3679BA) {
            background-image: linear-gradient(to right, $color1 60%, $color2 60%);
            background-size: 30px 100%;
        }

        .block {
            display: inline-block;
            position: relative;
            overflow: hidden;
            padding-left: 18px;
            padding-right: 18px;
            margin-left: -34px;

            .top, .bottom {
                height: 50%;
                border-right: solid 1px #ccc;
            }

            &:first-child {
                .top, .bottom {
                    margin-left: -18px;
                }
            }

            &:last-child {
                .top, .bottom {
                    margin-right: -36px;
                }
            }

            .text {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translateX(-50%) translateY(-50%);
                color: #fff;
                font-size: 20px;
                text-align: center;
                width: 100%;
                overflow: hidden;
            }

            .top {
                @include liner();
                transform: skewX(45deg);
            }

            .bottom {
                @include liner();
                transform: skewX(-45deg);
            }

            &.active {
                .top {
                    @include liner($color1: #18517C, $color2: #1E5888);
                }

                .bottom {
                    @include liner(#18517C, #1E5888);
                }
            }

        }

    }

</style>
