import { cloneDeep, assign } from 'lodash';
import { translateLicense } from '../../../../utils/tools';

const defaultState = {
    vehicleContactInfo: [],
    vehicleContactResult: {},
    vehicleQueryId: 1
};
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        setVehicleContactInfo (state, payload) {
            state.vehicleContactInfo = translateLicense(payload);
        },
        setVehicleContactResult (state, payload) {
            state.vehicleContactResult = payload;
        },
        initData (state) {
            assign(state, cloneDeep(defaultState));
        }

    }
};
