import { cloneDeep, assign } from 'lodash';

const defaultState = {
    duplicateResult: {
        name: '',
        maleNum: 0,
        femaleNum: 0,
        total: 0
    }
};
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        setDuplicateResult (state, payload) {
            state.duplicateResult.name = payload.name;
            state.duplicateResult.maleNum = payload.maleNum;
            state.duplicateResult.femaleNum = payload.femaleNum;
            state.duplicateResult.total = payload.femaleNum + payload.maleNum;
        },
        initData (state) {
            assign(state, cloneDeep(defaultState));
        }
    }
};
