import { cloneDeep, assign } from 'lodash';
import { translateLicense } from '../../../../utils/tools';

const defaultState = {
    businessInfo: '',
    certificatesInfo: [],
    completeInfo: {},

    notifyInfo: '',
    driverLicenseInfo: {},
    payQrUrl: '',
    lsh: ''
};

// 驾驶证换证（期满换证，超龄换证，遗失补证）
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        setBusinessInfo (state, payload) {
            state.businessInfo = payload;
        },
        setLSH (state, payload) {
            state.lsh = payload;
        },
        setCertificatesInfo (state, payload) {
            state.certificatesInfo = translateLicense(payload);
        },
        setCompleteInfo (state, payload) {
            state.completeInfo = payload;
        },
        setDriverLicenseInfo (state, payload) {
            state.driverLicenseInfo = payload;
        },
        setNotifyInfo (state, payload) {
            state.notifyInfo = payload;
        },
        setPayQrUrl (state, payload) {
            state.payQrUrl = payload;
        },
        initData (state) {
            assign(state, cloneDeep(defaultState));
        }
    }
};
