<template>
    <div style="height: 100vh">
        <div class="kd-message-title timeTips">温馨提示</div>
        <div class="kd-content-wrapper content">
            <div v-if="!tipPage.imgName" class="tipsIcon kedaIconfont" :class="{'kedaIcon-xiaolian':!tipPage.type, 'kedaIcon-smile':tipPage.type}"></div>
            <div v-else class="imgSize" :class="tipPage.imgName"></div>
            <p class="tipsContent" v-html="tipPage.message"></p>
            <div class="kd-foot-button">
                <kd-button class="home" icon="kedaIcon-policework" text="首页" type-class="home" :button-handle="goHome"/>
                <kd-button v-if="tipPage.enableBack" icon="kedaIcon-duihao2" :text="tipPage.backName" :button-handle="back"/>
                <kd-button v-if="tipPage.extraBut" :icon="tipPage.extraIcon" :text="tipPage.extraText" :button-handle="tipPage.extraFun"/>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { logout } from '@/utils';

export default {
    name: 'Tip',
    props: {
        backPre: {
            default: null
        }
    },
    methods: {
        ...mapMutations('common', ['changeVisible', 'setTipPage']),
        back () {
            if (this.backPre) { // 返回上一步
                this.backPre();
            }
            this.changeVisible({ visibleTip: false, visibleSteps: true, visibleBusinessName: true });
        },
        goHome () {
            logout();
        }
    },
    computed: {
        ...mapState('common', {
            tipPage: 'tipPage'
        })
    },
    beforeDestroy () {
        this.setTipPage({
            type: 0,
            message: '暂无内容',
            url: '/'
        });
    }
};
</script>

