<template>
	<div
			class="wrap"
			:class="{'isFocus':isFocus,'textureWrap':isTexture}"
	>
		<div
				class="input-wrap"
				:class="{'kdTexture':isTexture,'kdInput':!isTexture}"
		>
			<input
					v-if="!isTexture"
					v-model="data"
					:placeholder="placeholder"
					@click="startKeyBoard($event)"
					@blur="endKeyBoard($event)"
					ref="element"
					:disabled="disabled"
			>
			<textarea
					v-if="isTexture"
					:rows="rows"
					v-model="data"
					@click="startKeyBoard($event)"
					@blur="endKeyBoard($event)"
					ref="element"
					:disabled="disabled"
			></textarea>
			<i
					v-if="showClearIcon"
					class="handInput kedaIconfont kedaIcon-shanchumr1"
					@click="clear"
			></i>
		</div>
	</div>
</template>

<script>
import { keyboardShow, keyboardByHand, keyboardByIdNumber } from '@/service/common';

export default {
    name: 'KdInput',
    props: {
        placeholder: String, // 输入提示
        defaultContent: {
            default: ''// 默认值
        },
        isTexture: Boolean, // 是否多行输入
        rows: Number, // 多行输入框的行数
        imeType: {
            default: 1// 输入法的类别：0:无键盘；1:手写；2:普通键盘;3:身份证键盘
        },
        disabled: {
            default: false// 禁用输入框
        },
        limitNumber: {
            default: 0// 限制输入位数
        },
        watchData: Function, // 输入的值改变的数据返回上层组件
        upperCase: Boolean, // 是否修改字母为大写
        getFocus: Function
    },
    data () {
        return {
            isFocus: false,
            data: '',
            showClearIcon: false
        };
    },
    watch: {
        defaultContent (newVal) {
            this.changeData(newVal);
        },
        data (newVal) {
            if (this.upperCase) {
                this.data = this.data.toLocaleUpperCase();
            }

            if (this.limitNumber && this.data.length > this.limitNumber) {
                this.data = this.data.substring(0, this.limitNumber);
                return false;
            }

            if (this.watchData) {
                this.watchData(newVal);
                this.showClearIcon = true;
            }

            if (window.document.onclick) {
                window.document.onclick();
            }
        }
    },
    methods: {
        clear () {
            this.showClearIcon = false;
            this.data = '';
            this.startKeyBoard();
        },
        changeData (val) {
            this.data = val;
        },
        startKeyBoard (event) {
            // 点击输入框触发
            if (event && event.preventDefault) {
                event.preventDefault();
            }
            this.isFocus = true;
            this.$refs.element.focus();
            if (this.imeType === 0) { // 无键盘
                if (this.getFocus) {
                    this.getFocus(this); // 无三方键盘时候点击将组件和页面键盘绑定或者不传，用refs
                }
            } else if (this.imeType === 1) { // 手写
                keyboardByHand({});
            } else if (this.imeType === 2) { // 普通键盘
                keyboardShow({});
            } else if (this.imeType === 3) { // 身份证键盘
                keyboardByIdNumber({});
            }
        },
        endKeyBoard (event) {
            // input聚焦
            if (event && event.preventDefault) {
                event.preventDefault();
            }
            this.isFocus = false;
        },
        setElementCursorPos (position) {
            // 设置光标位置
            this.$refs.element.setSelectionRange(position, position);
        },
        mouseClick (data) {
            // 页面键盘输入字符时候处理
            this.isFocus = true;
            this.$refs.element.focus();
            const position = this.$refs.element.selectionStart;
            if (data === 'del') {
                if (position > 0) {
                    this.data = this.data.slice(0, position - 1) + this.data.slice(position);
                    setTimeout(() => {
                        this.setElementCursorPos(position - 1, position - 1);
                    }, 0);
                }
            } else {
                this.data = this.data.slice(0, position) + data + this.data.slice(position);
                setTimeout(() => {
                    this.setElementCursorPos(position + 1, position + 1);
                }, 0);
            }
        }
    }
};
</script>

<style lang="scss" scoped>
	.wrap {
		border-radius: 6px;
		height: 60px;
		border: 1px solid #fff;
		background: rgba(255, 255, 255, 0.8);
		box-sizing: border-box;
		position: relative;
	}

	.textureWrap {
		height: auto;
	}

	.input-wrap {
		position: relative;
		width: 100%;
		height: 100%;
	}

	.kdInput {
		height: 100%;

		input {
			width: 100%;
			height: 100%;
			padding: 0;
			text-indent: 20px;
			color: inherit;
			font-size: inherit;
			line-height: inherit;
			border: none;
			outline: none;
			box-sizing: border-box;
			position: absolute;
			background: none;
			left: 0;
			top: 0;
		}

		input::-webkit-input-placeholder {
			color: #c0c4cc;
		}
	}

	.kdTexture {
		height: 150px;

		textarea {
			width: 100%;
			height: 100%;
			margin: 0;
			padding: 0;
			background: none;
			display: block;
			color: inherit;
			font-size: inherit;
			line-height: inherit;
			text-indent: 20px;
			border: none;
			outline: none;
			position: absolute;
			left: 0;
			top: 0;
		}
	}

	.handInput {
		font-size: 19.2px;
		height: 100%;
		position: absolute;
		right: 0;
		bottom: 0;
		display: flex;
		align-items: center;
		padding: 0 10px;
	}

	.isFocus {
		border: 1px solid #01f3ff;
	}

</style>
