import { cloneDeep, assign } from 'lodash';
import { translateLicense } from '@/utils/tools';

const defaultState = {
    businessInfo: '', // 业务须知信息
    licenceInfo: {},
    completeInfo: {},
    delayPostInfo: {}, // 邮寄信息
    reasonSaved: '',
    materialPhoto: [] // 上传材料
};
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        setBusinessInfo (state, payload) {
            state.businessInfo = payload;
        },
        setLicenceInfo (state, payload) {
            delete payload.delayCardId;

            state.licenceInfo = translateLicense(payload);
        },
        setDelayPostInfo (state, payload) {
            state.delayPostInfo = payload;
        },
        setReason (state, payload) {
            state.reasonSaved = payload;
        },
        setMaterialPhoto (state, payload) {
            state.materialPhoto = payload;
        },
        setCompleteInfo (state, payload) {
            state.completeInfo = payload;
        },
        initData (state) {
            assign(state, cloneDeep(defaultState));
        }
    }
};
