import Vue from 'vue';
import VueRouter from 'vue-router';

let router = null;

// 路由配置
const RouterConfig = {
    routes: [
        {
            path: '/',
            name: 'menu',
            component: () => import('@/view/public/menu/indexN')
        }, // 一级菜单
        {
            path: '/subMenu',
            name: 'subMenu',
            component: () => import('@/view/public/menu/menu')
        }, // 二级菜单
        {
            path: '/noBusiness',
            name: 'noBusiness',
            component: () => import('@/view/public/noBusiness/index')
        }
    ]
};

Vue.use(VueRouter);

router = new VueRouter(RouterConfig);

export default router;
