import { cloneDeep, assign } from 'lodash';
import { addPrefix } from '@/utils/tools';
import { generateBusinessId } from '@/service/common';
import { changeRouter } from '@/utils';
import { Message } from 'element-ui';

// bornDay: '19950403',
// certAddress: '江苏省园区腾飞创新园',
// certNumber: '320982299402030392',
// certOrg: '园区公安局',
// effDate: '20181004',
// expDate: '20321004',
// gender: 1,
// identityPic: 'wefwefew',
// nation: '汉',
// partyName: '过渡性'

const defaultState = {
    steps: [],
    menuList: [],

    visibleSteps: false,
    visibleTip: false, // 错误提示页面
    visibleBusinessName: false, // 当前在办理业务提示
    visibleJurisdiction: false, // 倒数计时器

    dogHandlePic: '',

    currentStep: 0,
    identifyCard: {},
    identifyCardPicture: '',
    binocularCamera: '', // 人脸识别照片
    userRecognitionId: null,
    userBusinessId: null, // 每个业务Id，需要逐步从相应的store里提取出来
    tipPage: {
        type: 0,
        enableBack: false,
        message: '暂无内容',
        backName: '返回',
        extraBut: false,
        extraIcon: '',
        extraText: '',
        extraFun: null
    }, // 0 哭，1 笑
    businessTypeCode: null,
    businessTypeName: '', // 当前办理业务名称
    payMethod: null,
    singleReflexHandlePhoto: null, // 单反拍完处理后的照片数据
    printImgUrls: [], // 预览图片id
    printId: null, // 打印pdf id
    currentPage: 1, // 当前页数
    menuState: 1, // 1:显示一级导航2：显示二级导航
    menuId: 1 // 菜单一级Id
};
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        changeMenuPageState (state, payload) {
            if (payload.currentPage) {
                state.currentPage = payload.currentPage;
            }
            if (payload.menuState) {
                state.menuState = payload.menuState;
            }
            if (payload.menuId) {
                state.menuId = payload.menuId;
            }
        },
        setPrintInfo (state, payload) {
            state.printId = payload.fileName;
            state.printImgUrls = payload.images.map((item) => {
                return addPrefix(item);
            });
        },
        setPrintImgUrls (state, payload) {
            state.printImgUrls.push(payload);
        },
        setSingleReflexHandlePhoto (state, payload) {
            state.singleReflexHandlePhoto = addPrefix(payload);
        },
        setPayMethod (state, payload) {
            state.payMethod = payload;
        },
        changeTipPage (state, payload) {
            if (!payload.message) {
                payload.message = '操作失败，请稍后再试......';
            }
            payload.message = payload.msg ? payload.msg : payload.message;
            if (payload.message === 'Network Error') {
                payload.message = '网络异常，请稍后再试......';
            }
            payload.enableBack = !!payload.enableBack;
            payload.type = payload.type ? payload.type : 0;
            state.tipPage = { ...state.tipPage, ...payload };
            state.visibleTip = true;
            state.visibleSteps = false;
            state.visibleBusinessName = false;
        },
        changeStep (state, payload) {
            const steps = cloneDeep(state.steps);
            let currentStep = state.currentStep;
            let itemIndex = -1;

            if (steps && steps.length > 0) {
                steps.forEach((item) => {
                    item.isActive = false;
                });

                if (payload === 'up') { // back:返回首页;down:向下;up:向上;具体key：跳转到相应的位置
                    if (currentStep <= 0) {
                        return;
                    }

                    if ((steps[currentStep].name === steps[currentStep - 1].name) || !steps[currentStep - 1].visiable) {
                        itemIndex = steps.findIndex((item) => {
                            return steps[currentStep - 1].name === item.name;
                        });
                        if (itemIndex >= 0) {
                            steps[itemIndex].isActive = true;
                        }
                    } else {
                        steps[currentStep - 1].isActive = true;
                    }
                    currentStep = currentStep - 1;
                } else if (payload === 'down') {
                    if (currentStep >= (steps.length - 1)) {
                        return;
                    }
                    if ((steps[currentStep].name === steps[currentStep + 1].name) || !steps[currentStep + 1].visiable) {
                        itemIndex = steps.findIndex((item) => {
                            return steps[currentStep + 1].name === item.name;
                        });
                        if (itemIndex >= 0) {
                            steps[itemIndex].isActive = true;
                        }
                    } else {
                        steps[currentStep + 1].isActive = true;
                    }

                    currentStep = currentStep + 1;
                } else if (payload === 'back') {
                    steps[0].isActive = true;
                    currentStep = 0;
                } else {
                    itemIndex = steps.findIndex((item) => {
                        return payload === item.key;
                    });
                    if (itemIndex >= 0) {
                        const name = steps[itemIndex].name;
                        const matchIndex = steps.findIndex((item) => {
                            return name === item.name;
                        });
                        steps[matchIndex].isActive = true;
                        currentStep = itemIndex;
                    }
                }
                state.steps = steps;
                state.currentStep = currentStep;
            }
        },
        setUserRecognitionId (state, payload) {
            state.userRecognitionId = payload;
        },
        setUserBusinessId (state, payload) {
            state.userBusinessId = payload;
        },
        setMenuList (state, payload) {
            state.menuList = payload;
        },
        setDogHandlePic (state, payload) {
            state.dogHandlePic = payload;
        },
        setIdentifyCard (state, payload) {
            payload.identityPic = addPrefix(payload.identityPic);
            state.identifyCard = payload;
        },
        setIdentifyCardPhoto (state, payload) {
            state.identifyCard.identityPic = addPrefix(payload);
        },
        setBinocularCamera (state, payload) {
            state.binocularCamera = addPrefix(payload);
        },
        setTipPage (state, payload) {
            state.tipPage = { ...state.tipPage, ...payload };
        },
        setSteps (state, payload) {
            state.steps = payload;
        },
        setCurrentBusinessHandle (state, payload) {
            state.businessTypeCode = payload;
        },
        setCurrentBusinessName (state, payload) {
            state.businessTypeName = payload;
        },
        changeVisible (state, payload) {
            if (payload.visibleSteps !== undefined) {
                state.visibleSteps = payload.visibleSteps;
            }
            if (payload.visibleTip !== undefined) {
                state.visibleTip = payload.visibleTip;
            }
            if (payload.visibleBusinessName !== undefined) {
                state.visibleBusinessName = payload.visibleBusinessName;
            }
            if (payload.visibleJurisdiction !== undefined) {
                state.visibleJurisdiction = payload.visibleJurisdiction;
            }
        },
        setIdentifyCardPicture (state, payload) {
            state.identifyCardPicture = payload;
        },
        initData (state) {
            assign(state, cloneDeep(defaultState));
        }
    },
    actions: {
        async businessStart ({ state, commit }, payload) {
            const res = await generateBusinessId({});
            if (res && res.code === '0') {
                commit('setUserBusinessId', res.result);
                commit('changeVisible', {
                    visibleSteps: true,
                    visibleTip: false,
                    visibleBusinessName: true,
                    visibleJurisdiction: true
                });
                changeRouter(payload.path);
            } else if (res) {
                changeRouter('/noBusiness');
                Message.error(res.message || '此业务暂未开启，请联系管理员');
            }
        }
    }
};
