import { cloneDeep } from 'lodash';

// 用于截图每个步骤

const defaultState = {
    images: {}
};
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        setImages (state, payload) {
            if (!state.images[payload.code]) {
                state.images[payload.code] = [];
            }
            state.images[payload.code].push(payload.base64);
        },
        clearImages (state, payload) {
            state.images[payload.code] = [];
        },
        initData (state, payload) {
        }
    },
    actions: {}
};
