import { PHOTO_TYPE_ICON } from '@/constant/constant';
import { cloneDeep, assign } from 'lodash';

const defaultState = {
    typeList: [], // 证件照类别列表
    photoType: {},		// 选择的证件类型信息
    category: null,	// 证件类别
    visa: null,		// 签证类别
    chosePhoto: null,					// 选择的打印的照片
    previewPrintPhoto: null,			// 排版好的照片
    printCount: 1,						// 默认打印的份数
    price: 15,		// 打印一份照片的单价
    totalMoney: 15,	// 打印照片总金额
    photoPrintInfoId: null,	// 打印照片信息的id
    result: {},
    qriously: '', // 二维码数据
    outTradeNo: '' // 支付宝订单号
};
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        setTypeList (state, payload) {
            payload.forEach((item) => {
                const iconData = PHOTO_TYPE_ICON.find((i) => {
                    return i.name === item.name;
                });
                if (iconData) {
                    item.icon = iconData.icon;
                    if (iconData.addColor) {
                        item.childTypes.forEach((j) => {
                            j.img = iconData.addColor[j.bgColor];
                        });
                    }
                }
            });
            state.typeList = payload;
        },
        // 更新证件照业务
        setPhotoType (state, payload) {
            state.photoType = payload;
            if (payload.price) {
                state.price = payload.price;
            }
        },
        // 更新证件照类别
        setCategory (state, payload) {
            state.category = payload;
        },
        // 更新证件照签证
        setVisa (state, payload) {
            state.visa = payload;
        },
        // 设置选择的照片
        setChosePhoto (state, payload) {
            state.chosePhoto = payload;
        },
        // 设置排版好的照片
        setPreviewPrintPhoto (state, payload) {
            state.previewPrintPhoto = payload;
        },
        // 设置打印的份数
        setPrintCount (state, payload) {
            state.printCount = payload;
        },
        // 设置打印总金额
        setTotalMoney (state, payload) {
            state.totalMoney = payload;
        },
        // 保存打印照片信息的id
        setPrintInfoId (state, payload) {
            state.photoPrintInfoId = payload;
        },
        setPrintResult (state, payload) {
            state.result = payload;
        },
        setQriously (state, payload) {
            state.qriously = payload;
        },
        setOutTradeNo (state, payload) {
            state.outTradeNo = payload;
        },
        initData (state) {
            assign(state, cloneDeep(defaultState));
        }
    }
};
