<template>
    <div>
        <div class="wrap">
            <div class="contentText" @click="startKeyBoard()">{{ finalData ? finalData : placeholder }}</div>
            <div v-show="isFull" class="fullWrap" @click="hideKeyBord($event)">
                <div class="inputTop">
                    <el-button type="primary" class="outBut" @click="hideKeyBord($event)">退出</el-button>
                    <div class="wrap input-wrap kdInput" :class="{'isFocus':isFocus}">
                        <input
                            v-model="data"
                            :placeholder="placeholder"
                            @click="startKeyBoard($event)"
                            @blur="endKeyBoard($event)"
                            ref="element"
                            :disabled="disabled"
                        >
                        <i v-if="showClearIcon" class="handInput kedaIconfont kedaIcon-shanchumr1" @click="clear($event)"></i>
                    </div>
                    <el-button type="primary" @click="sure($event)" class="sureBut">确认</el-button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { keyboardShow, keyboardByHand, keyboardByIdNumber, keyboardHide } from '@/service/common';

export default {
    name: 'KdFullInput',
    props: {
        placeholder: String, // 输入提示
        defaultContent: {
            default: ''// 默认值
        },
        imeType: {
            default: 1// 输入法的类别：0:无键盘；1:手写；2:普通键盘;3:身份证键盘
        },
        disabled: {
            default: false// 禁用输入框
        },
        limitNumber: {
            default: 0// 限制输入位数
        },
        watchData: Function, // 输入的值改变的数据返回上层组件
        upperCase: Boolean, // 是否修改字母为大写
        getFocus: Function
    },
    data () {
        return {
            isFull: false,
            isFocus: false,
            data: '',
            finalData: '',
            showClearIcon: false
        };
    },
    mounted () {
        this.changeData(this.defaultContent);
    },
    watch: {
        defaultContent (newVal) {
            this.changeData(newVal);
        },
        data (newVal) {
            if (window.document.onclick) {
                window.document.onclick();
            }
            if (this.upperCase) {
                this.data = this.data.toLocaleUpperCase();
            }
            if (this.limitNumber && this.data.length > this.limitNumber) {
                this.data = this.data.substring(0, this.limitNumber);
                return false;
            }
            if (this.data) {
                this.showClearIcon = true;
            }
        }
    },
    beforeDestroy () {
        this.hideKeyBord();
    },
    methods: {
        sure (event) {
            if (event && event.stopPropagation) {
                event.stopPropagation();
            }
            if (this.watchData) {
                this.finalData = this.data;
                this.watchData(this.finalData);
            }
            this.hideKeyBord();
        },
        hideKeyBord (event) {
            if (event && event.stopPropagation) {
                event.stopPropagation();
            }
            this.data = '';
            this.isFull = false;
            keyboardHide();
        },
        clear (event) {
            if (event && event.stopPropagation) {
                event.stopPropagation();
            }
            this.showClearIcon = false;
            this.data = '';
            // this.startKeyBoard();
        },
        changeData (val) {
            this.data = this.finalData = val;
            if (this.watchData) {
                this.watchData(this.data);
            }
        },
        startKeyBoard (event) {
            if (!event) {
                // 打开全屏键盘的时候设置默认值
                this.changeData(this.finalData);
            }

            this.isFull = true;
            // 点击输入框触发
            if (event && event.preventDefault) {
                event.preventDefault();
            }
            if (event && event.stopPropagation) {
                event.stopPropagation();
            }
            this.isFocus = true;
            if (this.imeType === 0) { // 无键盘
                this.getFocus(this); // 无三方键盘时候点击将组件和页面键盘绑定
            } else if (this.imeType === 1) { // 手写
                keyboardByHand({});
            } else if (this.imeType === 2) { // 普通键盘
                keyboardShow({});
            } else if (this.imeType === 3) { // 身份证键盘
                keyboardByIdNumber({});
            }
            setTimeout(() => {
                this.$refs.element.focus();
            }, 500);
        },
        endKeyBoard (event) {
            // input聚焦
            if (event && event.preventDefault) {
                event.preventDefault();
            }
            this.isFocus = false;
        },
        setElementCursorPos (position) {
            // 设置光标位置
            this.$refs.element.setSelectionRange(position, position);
        },
        mouseClick (data) {
            // 页面键盘输入字符时候处理
            this.isFocus = true;
            this.$refs.element.focus();
            const position = this.$refs.element.selectionStart;
            if (data === 'del') {
                if (position > 0) {
                    this.data = this.data.slice(0, position - 1) + this.data.slice(position);
                    setTimeout(() => {
                        this.setElementCursorPos(position - 1, position - 1);
                    }, 0);
                }
            } else {
                this.data = this.data.slice(0, position) + data + this.data.slice(position);
                setTimeout(() => {
                    this.setElementCursorPos(position + 1, position + 1);
                }, 0);
            }
        }
    }
};
</script>

<style lang="scss" scoped>
    .wrap {
        border-radius: 6px;
        height: 60px;
        border: 1PX solid #fff;
        background: rgba(255, 255, 255, 0.8);
        box-sizing: border-box;
        position: relative;
        text-indent: 15px;
    }

    .fullWrap {
        position: fixed;
        z-index: 99;
        height: 100vh;
        width: 100vw;
        background: rgba(0, 0, 0, 0.7);
        top: 0;
        left: 0;

        .inputTop {
            height: 60px;
            width: 100vw;
            padding: 10px;
            display: flex;
            background: linear-gradient(180deg, rgba(197, 215, 234, 1) 0%, rgba(165, 191, 216, 1) 100%);
            position: absolute;
            bottom: 512PX;
            text-align: center;
            input {
                text-align: center;
                font-size: 32px;
            }
        }

        .input-wrap {
            position: relative;
            width: 100%;
            height: 100%;
            background: #ffffff;
            margin-right: 10px;
        }

        .sureBut {
            width: 100px;
            background: linear-gradient(180deg, rgba(251, 149, 39, 1) 0%, rgba(255, 115, 20, 1) 100%);
            border: 1px solid #ffffff;
            overflow: hidden;
            margin-right: 20px;
            font-size: 24px;
        }

        .outBut {
            background: linear-gradient(180deg, rgba(137, 189, 242, 1) 0%, rgba(79, 153, 225, 1) 100%);
            border: 1px solid #ffffff;
            overflow: hidden;
            margin-right: 10px;
            padding: 0 50px;
            font-size: 24px;
        }

    }

    .contentText {
        height: 100%;
        display: flex;
        align-items: center;
        color: #636F93;
        font-size: 28px;
    }

    .kdInput {
        height: 100%;

        input {
            width: 100%;
            height: 100%;
            padding: 0;
            text-indent: 20px;
            color: inherit;
            font-size: inherit;
            line-height: inherit;
            border: none;
            outline: none;
            box-sizing: border-box;
            position: absolute;
            background: none;
            left: 0;
            top: 0;
        }

        input::-webkit-input-placeholder {
            color: #c0c4cc;
        }
    }

    .kdTexture {
        height: 150px;

        textarea {
            width: 100%;
            height: 100%;
            margin: 0;
            padding: 0;
            background: none;
            display: block;
            color: inherit;
            font-size: inherit;
            line-height: inherit;
            text-indent: 20px;
            border: none;
            outline: none;
            position: absolute;
            left: 0;
            top: 0;
        }
    }

    .handInput {
        font-size: 19.2px;
        height: 100%;
        position: absolute;
        right: 0;
        bottom: 0;
        display: flex;
        align-items: center;
        padding: 0 10px;
    }

    .isFocus {
        border: 1px solid #01f3ff;
    }

</style>
