// 六年免检
import { cloneDeep, assign } from 'lodash';
import { translateLicense } from '../../../../utils/tools';

const defaultState = {
    examptionCarInfo: [], // 六年免检 机动车交强险信息
    uploadPhoto: '', // 上传材料照片
    examptionPostInfo: {}, // 六年免检 邮寄信息
    phoneNum: '', // 验证手机的手机号
    examptionCompleteInfo: {} // 六年免检 办理完成信息
};

export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        initData (state) {
            assign(state, cloneDeep(defaultState));
        },
        // 六年免检 机动车交强险信息
        setExamptionCarInfo (state, payload) {
            state.examptionCarInfo = translateLicense(payload);
        },
        // 上传材料保存照片
        setSavePhoto (state, payload) {
            state.uploadPhoto = payload;
        },
        // 六年免检 邮寄信息
        setExamptionPostInfo (state, payload) {
            state.examptionPostInfo = payload;
        },
        setPhone (state, payload) {
            state.phoneNum = payload;
        },
        // 六年免检 业务完成信息
        setExamptionCompleteInfo (state, payload) {
            state.examptionCompleteInfo = payload;
        }
    }
};
