<template>
    <button
        @click="nextHandle"
        class="kdButton"
        :class="[typeClass, butDisable ? 'butDisable' : '']"
        :disable="butDisable"
    >
        <i v-if="!isLoading" class="kedaIconfont" :class="icon"></i>
        <i v-if="isLoading" class="el-icon-loading"></i>
        <span v-if="!isLoading">{{ text }}</span>
        <span v-if="isLoading">{{ timeNumber }}</span>
    </button>
</template>

<script>
import { logout } from '@/utils';
import html2canvas from 'html2canvas';
import { mapState } from 'vuex';
import { savePhotoClient } from '@/service/common';

export default {
    name: 'KdButton',
    props: {
        icon: { type: String, default: '' },
        text: { type: String, default: '' },
        buttonHandle: { type: Function },
        typeClass: { type: String, default: '' },
        butDisable: { type: Boolean, default: false }
    },
    data () {
        return {
            isLoading: false,
            timeNumber: 1
        };
    },
    computed: {
        ...mapState('common', {
            businessTypeCode: 'businessTypeCode',
            currentStep: 'currentStep',
            steps: 'steps'
        })
    },
    methods: {
        async nextHandle () {
            if (this.butDisable) {
                return;
            }

            if (this.CONSTANT.enableScreenShot && this.steps && this.steps.length > 0) { // 存储步骤截图
                const name = this.$moment().format('YYYYMMDDHHmmss') + '-' + this.steps[this.currentStep].name + '-' + this.currentStep;
                const canvas = await html2canvas(document.body, {
                    allowTaint: true,
                    useCORS: false
                });
                savePhotoClient({
                    filePath: this.businessTypeCode,
                    fileName: name,
                    base64: canvas.toDataURL('image/jpeg', 0.5)
                });
            }

            if (this.buttonHandle) {
                if (!this.isLoading) {
                    this.isLoading = true;
                    this.startInterval();
                    await this.buttonHandle();
                    clearInterval(this.timeId);
                    this.timeId = null;
                    this.isLoading = false;
                }
            } else if (this.typeClass === 'home') {
                logout();
            } else if (this.typeClass === 'complete') {
                logout();
            }
        },
        startInterval () {
            this.timeNumber = 1;
            this.timeId = setInterval(() => {
                this.timeNumber++;
            }, 1000);
        }
    }
};
</script>

<style lang="scss" scoped>
    .kdButton {
        background: linear-gradient(#89BDF2, #4F99E1);
        height: 72px;
        padding: 0 44px;
        border-radius: 8px;
        cursor: pointer;
        white-space: nowrap;
        border: 1px solid #dcdfe6;
        box-sizing: border-box;
        color: #ffffff;
        margin: 0;
        display: inline-block;
        text-align: center;
        outline: 0;
        transition: .1s;

        i {
            font-size: 21px;
            margin-right: 6px;
            display: inline-block;
        }

        span {
            font-size: 24px;
            display: inline-block;
        }
    }

    .home {
        background: linear-gradient(#FB9527, #FF7314);
        float: left;
    }

    .butDisable {
        background: linear-gradient(180deg, rgba(197, 215, 234, 1) 0%, rgba(165, 191, 216, 1) 100%) !important;
    }
</style>
