import Vue from 'vue';
import moment from 'moment';
import qriously from 'vue-qriously';
import kdButton from './components/kd-button';
import kdInput from './components/kd-input';
import kdFullInput from './components/kd-full-input';

moment.locale('zh-cn');
Vue.use(qriously);

Vue.use({
    install: (Vue) => {
        Vue.component('kdButton', kdButton);
        Vue.component('kdInput', kdInput);
        Vue.component('kdFullInput', kdFullInput);
    }
});

// 改写弹框方法
Vue.prototype.$message.success = (text) => {
    Vue.prototype.$message({
        type: 'success',
        message: text,
        showClose: true
    });
};
Vue.prototype.$message.error = (text) => {
    Vue.prototype.$message({
        type: 'error',
        message: text,
        showClose: true
    });
};
Vue.prototype.$notify.warning = (text) => {
    Vue.prototype.$notify({
        title: '警告',
        message: text,
        duration: 0,
        type: 'warning'
    });
};
Vue.prototype.$notify.success = (text) => {
    Vue.prototype.$notify({
        title: '成功',
        message: text,
        type: 'success'
    });
};

window.moment = moment;
Vue.prototype.$moment = moment;
Vue.prototype.$momentFormat = (time) => {
    return time ? moment(time).format('YYYY年MM月DD日') : '';
};
Vue.prototype.$loading.full = (text) => {
    return Vue.prototype.$loading({
        lock: true,
        text: text || '正在加载，请稍后...',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
    });
};
