import { cloneDeep, assign } from 'lodash';

const defaultState = {
    liveRegisterInfo: {},
    liveHandleBusinessInfo: { businessType: 3 }
};
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        setLiveRegisterInfo (state, payload) {
            state.liveRegisterInfo = payload;
        },
        setLiveHandleBusinessInfo (state, payload) {
            state.liveHandleBusinessInfo = { ...state.liveHandleBusinessInfo, ...payload };
        },
        initData (state) {
            assign(state, cloneDeep(defaultState));
        }
    }
};
