import { cloneDeep, assign } from 'lodash';
import { translateLicense } from '@/utils/tools';

const defaultState = {
    account: '',
    startTime: '',
    endTime: '',
    result: {},
    list: []
};
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        setAccount (state, payload) {
            state.account = payload;
        },
        setStartTime (state, payload) {
            state.startTime = payload;
        },
        setEndTime (state, payload) {
            state.endTime = payload;
        },
        setResult (state, payload) {
            // 特殊处理，无法控制每页的数目，默认返回十个，拆分两页处理
            if (payload.list) {
                if (payload.list.length > 0) {
                    if (payload.sPage % 2 === 1) {
                        state.list = payload.list.slice(0, 5);
                    } else {
                        state.list = payload.list.slice(5, 10);
                    }
                } else {
                    state.list = payload.list;
                }
                delete payload.list;
            }

            state.result = translateLicense(payload);
        },
        initData (state) {
            assign(state, cloneDeep(defaultState));
        }
    }
};
