<template>
	<div id="app">
		<layout></layout>
	</div>
</template>

<script>
import { mapMutations, mapActions, mapState } from 'vuex';
import { getBusinessRouters } from '@/router/sub-router';
import layout from '@/view/public/layout';
import { getMenus } from '@/constant/menuN';
import { wsSend } from '@/utils/websocket';

export default {
    name: 'App',
    components: { layout },
    computed: {
        ...mapState('permanent', {
            highPhotoDialog: 'highPhotoDialog',
            lightWebSocket: 'lightWebSocket',
            singleReflexWebSocket: 'singleReflexWebSocket',
            signWebSocket: 'signWebSocket',
            fingerprintWebSocket: 'fingerprintWebSocket',
            monitorCameraWebSocket: 'monitorCameraWebSocket'
        })
    },
    mounted () {
        this.renderRoute();
        this.renderMenu();
        const self = this;
        // window.onbeforeunload = this.stopAllWs;
        window.onunload = function () {
            self.highPhotoDialog.get_actionType('closeSignal');
            wsSend(self.lightWebSocket, 'lightCtrl@C1:OFF;C2:OFF;B3:OFF;B4:OFF;B5:OFF;B6:OFF');
            wsSend(self.singleReflexWebSocket, { module: 'Camera', directive: 'stop' });
            wsSend(self.signWebSocket, { module: 'HwWriter', directive: 'Stop' });
            wsSend(self.fingerprintWebSocket, { module: 'Finger', directive: 'Stop' });
            wsSend(self.monitorCameraWebSocket, { directive: 'Stop' });
        };
        this.connectWebSocket({ ...this.CONSTANT });
    },
    methods: {
        ...mapMutations('permanent', ['setINDEX_MENU', 'setBUSINESS_EN_NAME_CODE', 'setBUSINESS_CODE_NAME']),
        ...mapActions('permanent', ['connectWebSocket']),
        renderRoute () {
            let subRouters = getBusinessRouters(this.CONSTANT.location, this.CONSTANT.isOlin);
            this.setBUSINESS_EN_NAME_CODE(subRouters);
            this.setBUSINESS_CODE_NAME(subRouters);
            subRouters = subRouters.map((item) => {
                return {
                    name: item.name,
                    path: '/' + item.code,
                    component: item.component
                };
            });
            this.$router.addRoutes(subRouters);
        },
        renderMenu () {
            // todo 新导航
            this.setINDEX_MENU(getMenus());
        }
    }
};
</script>

<style scoped>
	#app {
		width: 100%;
		height: 100%;
		color: #2c3e55;
	}
</style>
