import { cloneDeep, assign } from 'lodash';
import moment from 'moment';

const defaultState = {
    moduleId: null,
    detail: {},
    number: '', // 户号
    payResult: {
        tranDate: '', // 日期
        urlMsg: '', // 扫描二维码
        mapsSeqNo: '' // 流水号
    },
    payDetails: []
};
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        setModuleId (state, payload) {
            state.moduleId = payload;
        },
        setBkDetail (state, payload) {
            if (!payload.totalMoney) {
                payload.totalMoney = 0;
                payload.itemList.forEach((item) => {
                    item.date = Number(moment(item.date + '01').format('x'));
                    payload.totalMoney = Number(item.latefee) + Number(item.amount);
                });

                payload.itemList.sort((x, y) => {
                    return x.date - y.date;
                });

                if (payload.itemList.length > 1) {
                    const startTime = moment(payload.itemList[0].date).format('YYYY年MM');
                    const endTime = moment(payload.itemList[payload.itemList.length - 1].date).format('YYYY年MM');
                    payload.resultTime = startTime + '至' + endTime;
                } else if (payload.itemList.length === 1) {
                    payload.resultTime = moment(payload.itemList[0].date).format('YYYY年MM');
                }
            }

            state.detail = payload;
        },
        setNumber (state, payload) {
            state.number = payload;
        },
        setPayResult (state, payload) {
            state.payResult = payload;
        },
        setPayDetails (state, payload) {
            payload.payFeeDetailResList.forEach((item) => {
                item.tranDate = moment(item.tranDate).format('YYYY年MM月DD日');
            });
            state.payDetails = payload.payFeeDetailResList;
        },
        initData (state) {
            assign(state, cloneDeep(defaultState));
        }
    }
};
