/**
 * 人管业务-居住证相关
 */
import request from '@/utils/request';

/**
 合肥、苏州 居住证办理进度查询
 */
export const getLiveProcess = (params) => {
    return request.ajax('/ssm/jzzMgt/getJzzZt', 'post', params);
};
/**
 * 合肥 保存人脸识别信息
 * {
 * 	"idNum": "string",
 * 	"identifiedPhoto": "string",
 * }
 *
 */
export const faceResidencePermit = (params) => {
    return request.ajax('/ssm/jzzMgt/faceRecognition', 'post', params);
};
/**
 * 合肥 三级行政区划
 * 区-镇-居委会
 */
export const getDivisionList = (params) => {
    return request.ajax('/ssm/jzzMgt/queryXzqh', 'post', params);
};

/**
 合肥 居住证办理登记
 */
export const submitResidencePermit = (params) => {
    return request.ajax('/ssm/jzzMgt/submit', 'post', params);
};
