export const wsSend = (ws, message) => {
    // console.log('websocket发送消息', message);

    if (typeof message === 'object') {
        message = JSON.stringify(message);
    }

    // websocket关闭状态不发送消息
    if (ws && ws.readyState === 1) {
        ws.send(message);
    }
};

export const wsOnMessage = (ws, messageFun) => {
    if (ws) {
        if (messageFun) {
            ws.onmessage = (event) => {
                let res = event.data;
                if (typeof res === 'string') {
                    res = JSON.parse(event.data);
                }
                messageFun(res);
            };
        } else {
            ws.onmessage = null;
        }
    }
};
