import axios from 'axios';
import qs from 'qs';
import { LOCATION_IDARR } from '@/constant/constant';
import { matchUrl } from '@/utils/tools';
// import { Loading } from 'element-ui';
import store from '@/store';

let CONSTANT = {};
let macAddress = '';
let serverUrl = '';

// 解决跨域时丢失 cookie 问题
axios.defaults.withCredentials = false;
axios.defaults.timeout = 90000;

const initConfig = (datas) => {
    serverUrl = '';
    CONSTANT = datas;

    const data = LOCATION_IDARR.find((item) => {
        return item.value === CONSTANT.location;
    });
    if (data) {
        serverUrl = CONSTANT[data.urlName];
    }
};

const getServerUrl = () => {
    return serverUrl;
};

const injectionMac = (id) => {
    macAddress = id;
};

const keyBoard = async (url, method, params) => {
    const config = {
        url: url,
        method: method
    };

    if (method === 'get') {
        config.params = params;
    } else {
        config.data = params;
    }

    console.log(url, 'request>>>', params);
    try {
        const response = await axios(config);
        console.log(url, 'response>>>', response.data);
        return response.data;
    } catch (error) {
        console.log(error);
        return { code: '-1', message: '网络异常，请稍后再试！' };
    }
};
const hxClient = (url, method, params) => {
    return new Promise((resolve, reject) => {
        const config = {
            url: url,
            method: method,
            data: params
        };
        config.headers = {
            'Content-Type': 'application/x-www-form-urlencoded'
        };

        config.transformRequest = [function (data) {
            data = qs.stringify(data);
            return data;
        }];

        axios(config)
            .then(response => {
                if (!CONSTANT.disableHandRequest) {
                    resolve(response.data);
                }
            })
            .catch(error => {
                if (!CONSTANT.disableHandRequest) {
                    resolve(error);
                }
            });
    });
};
const ajax = async (url, method, params, specialHandle) => {
    params = params || {};
    specialHandle = specialHandle || {};
    params.userBusinessId = store.state.common.userBusinessId; // 所有接口带上业务ID, 退出后id会清空
    params.userRecognitionId = store.state.common.userRecognitionId; // 所有接口带上身份信息id, 退出后id会清空
    params.businessTypeCode = store.state.common.businessTypeCode;
    params.businessTypeName = store.state.common.businessTypeName;
    params.ytjId = store.state.permanent.machineId;

    if (specialHandle.isEnd) {
        // 结束业务，计算时长，无数据的时候停止调用
        if (!params.businessTypeCode || !params.businessTypeName) {
            return;
        }
    }

    const config = {};
    config.url = url;
    config.method = method;
    config.headers = {
        'Content-Type': 'application/json;charset=utf-8',
        'device-id': macAddress
    };

    if (method === 'get') {
        config.params = params;
    } else {
        config.data = params;
    }

    let reqUrl = '';

    if (!specialHandle.isLocalHttp) {
        if (specialHandle.isPictureHandle) { // 电子狗相关
            reqUrl = matchUrl(url, CONSTANT.electronicDogServer);
        } else if (specialHandle.isSavePhoto) { // 本地存储照片
            reqUrl = matchUrl(url, CONSTANT.photoSavePath);
        } else {
            if (params.businessTypeCode >= 503 && params.businessTypeCode < 600) {
                reqUrl = matchUrl(url, CONSTANT.endorsementProgressUrl);
            } else if (params.businessTypeCode === 501 && params.businessTypeCode === 502) {
                reqUrl = matchUrl(url, CONSTANT.endorsementUrl);
            } else {
                reqUrl = matchUrl(url, serverUrl);
            }
        }
        if (CONSTANT.isOlin) {
            config.url = CONSTANT.olinServer + '/fzjxtserver/access';
            config.headers['request-url'] = reqUrl;
            config.data = params;
        } else {
            config.url = reqUrl;
        }
    }

    if (!url.match('/ssm/portalLog/save')) {
        console.log(url, 'request>>>', params);
    }

    if (CONSTANT.disableHandRequest) {
        return false;
    }

    try {
        const response = await axios(config);
        if (!url.match('/ssm/portalLog/save')) {
            console.log(url, 'response>>>', response.data);
        }
        return response.data;
    } catch (error) {
        console.log(error);
        return { code: '-1', message: '网络异常，请稍后再试！' };
    }
};

export default {
    initConfig,
    injectionMac,
    keyBoard,
    hxClient,
    ajax,
    getServerUrl,
    CONSTANT
};
