import { cloneDeep, assign } from 'lodash';

const defaultState = {
    noCrimeRegisterInfo: { name: '' },
    noCrimeHandleBusinessInfo: {},
    noCrimeBusinessTypes: {
        1: '出国',
        2: '入职就业',
        3: '服兵役',
        4: '升学'
    },
    noCrimeMailInfo: {},
    noCrimeMaterialPhoto: [],
    noCrimeBusinessType: '1',
    noCrimePhone: ''
};
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        setNoCrimeRegisterInfo (state, payload) {
            state.noCrimeRegisterInfo = { ...state.noCrimeRegisterInfo, ...payload };
        },
        setNoCrimeMailInfo (state, payload) {
            state.noCrimeMailInfo = { ...state.noCrimeMailInfo, ...payload };
        },
        setNoCrimeHandleBusinessInfo (state, payload) {
            state.noCrimeHandleBusinessInfo = { ...state.noCrimeHandleBusinessInfo, ...payload };
        },
        setNoCrimeBusinessTypes (state, payload) {
            state.noCrimeBusinessTypes = payload;
        },
        setNoCrimeMaterialPhoto (state, payload) {
            state.noCrimeMaterialPhoto = payload;
        },
        setNoCrimePhone (state, payload) {
            state.noCrimePhone = payload;
        },
        setNoCrimeBusinessType (state, payload) {
            if (payload) {
                payload.noCrimeBusinessType = String(payload);
            }
        },
        initData (state) {
            assign(state, cloneDeep(defaultState));
        }
    }
};
