import { cloneDeep, assign } from 'lodash';

// 合肥的 注销户口证明
const defaultState = {
    cancelHouseRes: {} // 注销户口证明结果
};
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        initData (state) {
            assign(state, cloneDeep(defaultState));
        }
    }
};
