import Vue from 'vue';
import Vuex from 'vuex';
import permanent from './module/permanent';
import common from './module/common';
import canvasImg from './module/canvasImg';
import live from './module/human/handle/live';
import liveHefei from './module/human/liveHefei';
import noCrime from './module/human/noCrime';
import entryAndExit from './module/exitEntry/entryAndExit';
import exitRecord from './module/exitEntry/exitRecord';
import exitProcess from './module/exitEntry/exitProcess';
import driverLicense from './module/traffic/driver/driverLicense';
import vehicle from './module/traffic/vehicle/vehicle';
import recordedMotorVehicles from './module/traffic/vehicle/recordedMotorVehicles';
import mortgage from './module/traffic/vehicle/mortgage';
import examption from './module/traffic/vehicle/examption';
import licenseRenewal from './module/traffic/vehicle/licenseRenewal';
import illegality from './module/traffic/illegal/illegality';
import identificationPhoto from './module/convenient/identificationPhoto';
import lifePay from './module/convenient/lifePay';
import delayExchange from './module/traffic/driver/delayExchange';
import replacement from './module/traffic/driver/replacement';
import innocent from './module/human/prove/innocent';
import liveHandleProcess from './module/human/query/liveHandleProcess';
import idNumHandleProcess from './module/human/query/idNumHandleProcess';
import temporaryCardHandle from './module/human/handle/temporary-card';
import idCardChangeOver from './module/human/handle/idCardChangeOver';
import idCardProcessing from './module/human/handle/idCardProcessing';
import duplicateName from './module/human/duplicateName';
import relation from './module/human/prove/relation';
import temporary from './module/human/prove/temporary';
import cancelHouseHefei from './module/human/prove/cancelHouseHefei';
import socialSecurity from './module/socialSecurity/socialSecurity';
import accumulation from './module/socialSecurity/accumulation';
import businessGuide from './module/convenient/businessGuide';

Vue.use(Vuex);

const store = new Vuex.Store({
    modules: {
        permanent,
        socialSecurity,
        common,
        canvasImg,
        live,
        liveHefei,
        noCrime,
        entryAndExit,
        driverLicense,
        vehicle,
        recordedMotorVehicles,
        illegality,
        identificationPhoto,
        lifePay,
        mortgage,
        examption,
        licenseRenewal,
        delayExchange,
        replacement,
        accumulation, // 公积金
        innocent, // 合肥 无犯罪证明开证明的
        exitRecord,
        exitProcess,
        liveHandleProcess, // 合肥 居住证进度查询
        temporaryCardHandle, // 临时身份证办理
        duplicateName, // 重名查询
        relation, // 亲属关系证明
        temporary, // 临时身份证明
        cancelHouseHefei, // 注销户口证明
        idNumHandleProcess, // 身份证办证进度 苏州
        idCardChangeOver, // 身份证期满换证 合肥
        idCardProcessing, // 身份证自助受理
        businessGuide // 业务指南
    }
});

export default store;
