<template>
    <div
        class="csPosition"
    >
        <kd-button :button-handle="()=>{this.csgoto(0)}"/>
        <kd-button :button-handle="()=>{this.csgoto(-1);}"/>
        <kd-button :button-handle="()=>{this.csgoto(1);}"/>
    </div>
</template>

<script>
import { logout } from '@/utils';
import { mapMutations } from 'vuex';

export default {
    name: 'AlertMsg',

    methods: {
        ...mapMutations('common', ['changeStep']),
        csgoto (type) {
            if (type === 0) {
                logout();
            } else if (type === 1) {
                this.changeStep('down');
            } else if (type === -1) {
                this.changeStep('up');
            }
        }
    }
};
</script>

<style lang="scss" scoped>
    .csPosition {
        position: fixed;
        bottom: 0;
        left: 0;
        z-index: 9999;
        display: flex;

        button {
            /*background: none;*/
            border: none;
            width: 100px;
            height: 30px;
        }
    }
</style>
