<template>
    <div class="jurisdiction" :class="{danger: dangerTimeFlag}">
        <span v-if="visibleTime">{{ hasTime }}s</span>
    </div>
</template>

<script>
import { unloadVideoDevice, closeFaceDevice } from '@/utils/hardware';
import { logout } from '@/utils';

export default {
    name: 'Jurisdiction',
    data () {
        return {
            defaultTime: process.env.NODE_ENV === 'development' ? 99999 : 90, // 测试时间延长
            hasTime: process.env.NODE_ENV === 'development' ? 99999 : 90,
            autoInterval: null, // 自动计算倒计时秒
            innerTimeInterval: null, // 在5秒内不操作
            dangerTimeFlag: false,
            visibleTime: false
        };
    },
    methods: {
        beginUseHasTime () {
            this.clearTimer();
            this.clearTimeOut();
            if (!this.autoInterval) {
                this.autoInterval = setInterval(async () => { // 倒计时的计时器
                    --this.hasTime;
                    if (this.hasTime <= 10) { // <10s 计时器字变红
                        this.dangerTimeFlag = true;
                    }
                    if (this.hasTime === 0) { // 90s倒计时完返回首页
                        this.clearTimer();
                        await unloadVideoDevice();
                        await closeFaceDevice();
                        logout();
                    }
                }, 1000);
            }
        },
        listenerTime () {
            this.visibleTime = false;
            this.clearTimer();
            if (!this.innerTimeInterval) {
                this.hasTime = this.defaultTime;
                this.dangerTimeFlag = false;
                this.innerTimeInterval = setTimeout(() => {
                    // 检测10s内有无操作
                    this.clearTimeOut();
                    this.visibleTime = true;
                    this.beginUseHasTime();
                }, 10000);
            }
        },
        clearTimer () { // 清除计时器
            if (this.autoInterval) {
                clearInterval(this.autoInterval);
                this.autoInterval = null;
            }
        },
        clearTimeOut () { // 清除检测10s延时器
            if (this.innerTimeInterval) {
                clearInterval(this.innerTimeInterval);
                this.innerTimeInterval = null;
            }
        },
        bodyEvent (event) {
            if (event && event.stopPropagation) {
                event.stopPropagation();
            }
            this.clearTimer();
            this.listenerTime();
        }
    },
    mounted () {
        this.listenerTime();
        window.document.onclick = this.bodyEvent;
        // window.document.body.addEventListener('click', this.bodyEvent);
    },

    beforeDestroy () {
        this.clearTimer();
        this.clearTimeOut();
        window.document.onclick = null;
        // window.document.body.removeEventListener('click', this.bodyEvent);
    }
};
</script>

<style lang="scss" scoped>
    .jurisdiction {
        text-align: right;
        line-height: 85px;
        font-size: 30px;
        padding: 0 20px;
        color: #ffffff;
        position: absolute;
        width: 100%;
        box-sizing: border-box;
    }

    .none {
        visibility: hidden;
    }

    .danger {
        color: red;
    }
</style>
