<template>
    <div class="warp" :style="bgCss">
        <el-dialog
            width="80vw"
            title="管理"
            :visible.sync="dialogTableVisible"
            :show-close="false"
            :center="true"
            :destroy-on-close="true"
            :before-close="closeDialog"
        >
            <div class="inputWrap">
                <kd-input class="inputContent" ref="inputContent" :imeType="0"/>
                <el-button class="inputSure" type="primary" @click="sureAdmin">确认</el-button>
            </div>
            <keyboard :get-keyboard-data="getKeyboardData"/>
        </el-dialog>
        <div>
            <img class="check" :src="policeLogo" alt="切换">
            <span>{{ title }}</span>
        </div>
        <div>
            <div class="machineText" @click="dialogTableVisible=true">终端编号：{{ machineNum }}</div>
            <div class="divider" :style="lineLeftColor"></div>
            <div class="divider" :style="lineRightColor"></div>
            <div class="week-and-day">
                <p>{{ day }}</p>
                <p>{{ week }}</p>
            </div>
            <p class="pull-right">
                {{ hour }}
            </p>
        </div>
    </div>
</template>

<script>
import policeLogo from '@/assets/images/police_logo.png';
import keyboard from '@/components/keyboard';
import { closeChrome } from '@/service/common';
import { GET_ADMIN_JURISDICTION } from '@/constant/constant';

export default {
    name: 'PageHead',
    props: ['machineNum'],
    components: { keyboard },
    watch: {
        $route () {
            this.changeBgColor();
        }
    },
    data () {
        return {
            dialogTableVisible: false,
            policeLogo: policeLogo,
            title: window.document.title,
            hour: '',
            day: '',
            week: '',
            timer: null,
            bgCss: {
                background: '',
                color: ''
            },
            lineLeftColor: { background: '' },
            lineRightColor: { background: '' }
        };
    },
    methods: {
        getKeyboardData (data) {
            this.$refs.inputContent.mouseClick(data);
        },
        closeDialog (done) {
            this.$refs.inputContent.changeData('');
            done();
        },
        sureAdmin () {
            if (this.$refs.inputContent.data === GET_ADMIN_JURISDICTION) {
                closeChrome({ operation: 'Close' });
            }
        },
        changeBgColor () {
            if (this.$route.path === '/' || this.$route.path === '/subMenu' || this.$route.path === '/subMenu') {
                this.bgCss.color = '#FFFFFF';
                this.bgCss.background = 'none';
                this.lineLeftColor.background = '#1558B2';
                this.lineRightColor.background = '#619AE0';
            } else {
                this.bgCss.color = '#000F3C';
                this.bgCss.background = 'linear-gradient(180deg, rgba(232, 235, 244, 1) 0%, rgba(210, 212, 225, 1) 100%)';
                this.lineLeftColor.background = '#C4C6D3';
                this.lineRightColor.background = '#EFF2FB';
            }
        }
    },
    mounted () {
        this.changeBgColor();
        const changeTime = () => {
            this.hour = this.$moment(Date.now()).format('HH:mm:ss');
            this.day = this.$moment(Date.now()).format('YYYY-MM-DD');
            this.week = this.$moment(Date.now()).format('dddd');
            if (this.hour === '00:00:00') {
                window.location.reload();
            }
        };
        this.timer = setInterval(changeTime, 1000);
    },
    beforeDestroy () {
        clearInterval(this.timer);
    }
};
</script>

<style lang="scss" scoped>
    .warp {
        height: 80px;
        background: linear-gradient(180deg, rgba(232, 235, 244, 1) 0%, rgba(210, 212, 225, 1) 100%);
        color: #000F3C;
        /*font-size: 20px;*/
        padding: 0 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .divider {
            height: 30px;
            width: 1px;
        }

        > div {
            display: flex;
            align-items: center;

            > span {
                padding-left: 15px;
                font-size: 26px;
            }
        }

        .machineText {
            font-size: 20px;
            margin-right: 20px;
        }

        .week-and-day {
            overflow: hidden;
            box-sizing: border-box;
            font-size: 16px;
            margin: 0 20px;

            > p {
                padding: 1px 0;
            }
        }
    }
    .inputWrap {
        display: flex;

        .inputContent {
            width: 100%;
            border: 1PX solid #ccc;/*no*/
            border-radius: 4px;
        }

        .inputSure {
            margin-left: 10px;
        }
    }

</style>
