import store from '@/store';
import moment from 'moment';

window.logArr = [];

const handleLogInfo = (payload, level) => {
    let logString = payload;

    if (Array.isArray(payload)) {
        logString = payload.map((item) => {
            return JSON.stringify(item);
        }).join(' ');
    }

    logString = moment().format('YYYY-MM-DD HH:mm:ss') + ' ' + logString;

    window.logArr.push({
        level: level,
        businessTypeCode: store.state.common.businessTypeCode,
        content: logString
    });
};

if (console) {
    var _console = {
        log: console.log,
        info: console.info,
        debug: console.debug,
        warn: console.warn,
        error: console.error
    };
    console.log = function (...payload) {
        handleLogInfo(payload, 'log');
        _console.log.apply(this, Array.prototype.slice.call(arguments, 0));
    };
    console.info = function (...payload) {
        handleLogInfo(payload, 'info');
        _console.info.apply(this, Array.prototype.slice.call(arguments, 0));
    };
    console.debug = function (...payload) {
        handleLogInfo(payload, 'debug');
        _console.debug.apply(this, Array.prototype.slice.call(arguments, 0));
    };
    console.warn = function (...payload) {
        handleLogInfo(payload, 'warn');
        _console.warn.apply(this, Array.prototype.slice.call(arguments, 0));
    };
    console.error = function (...payload) {
        handleLogInfo(payload, 'error');
        _console.error.apply(this, Array.prototype.slice.call(arguments, 0));
    };
}
