import { cloneDeep, assign } from 'lodash';

// 临时身份证证明
const defaultState = {
    userInfo: {},
    pdfUrl: '',
    temporaryReason: [], // 临时身份证证明办理原因
    temporaryRes: {}, // 临时身份证证明结果
    resultData: {}
};
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        setUserInfo (state, payload) {
            state.userInfo = payload;
        },
        setPdfUrl (state, payload) {
            state.pdfUrl = payload;
        },
        setTemporaryReason (state, payload) {
            state.temporaryReason = payload.map((item, index) => {
                return {
                    type: Object.keys(item).join(''),
                    value: Object.values(item).join('')
                };
            });
        },

        setTemporaryRes (state, payload) {
            // let arr = [];
            // arr.push({label: '姓名：', payloadue: payload.xm});
            // arr.push({label: '性别：', payloadue: payload.xb});
            // arr.push({label: '户主姓名：', payloadue: payload.hzxm});
            // arr.push({label: '与户主关系：', payloadue: payload.yhzgx});
            // arr.push({label: '户号：', payloadue: payload.hh});
            // arr.push({label: '身份证号：', payloadue: payload.gmsfzh});
            // arr.push({label: '民族：', payloadue: payload.mz});
            // arr.push({label: '出生日期：', payloadue: moment(payload.csrq).format('YYYY年MM月DD日')});
            // arr.push({label: '住址：', payloadue: payload.dzxx});
            // arr.push({label: '籍贯：', payloadue: payload.jgssxq});
            // arr.push({label: '数据归集单位：', payloadue: payload.sjgjdw});
            // arr.push({label: '人口状态：', payloadue: payload.ryzt});
            state.temporaryRes = payload;
        },
        setResultData (state, payload) {
            state.resultData = payload;
        },
        initData (state) {
            assign(state, cloneDeep(defaultState));
        }
    }
};
