import { cloneDeep, assign } from 'lodash';
import { addPrefix } from '@/utils/tools';
import { LEFT_FINGER_POSITION } from '@/constant/constant';

// 身份证自助受理

const defaultState = {
    checkResultInfos: {
        zwyZwdm: '', // "指纹一_指位",
        zwyZwtxsj: '', // "指纹一_图像数据",
        zwyZwtxzlz: '', // "指纹一_图像质量值",
        zwyZwtzsj: '', // "指纹一_特征数据",

        zweZwdm: '', // "指纹二_指位",
        zweZwtxsj: '', // "指纹二_图像数据",
        zweZwtxzlz: '', // " 指纹二_图像质量值",
        zweZwtzsj: ''// "指纹二_特征数据"
    },
    machineAddress: '', // 一体机设备地址 银川市贺兰县海亮警务室旁24小时自助服务大厅自助一体机
    selfCollectionAddress: '', // 自助领取地址 银川市贺兰县城关派出所
    queryIdPhoto: '', // "历史照片",
    phoneNumber: '', // 手机号码
    acceptanceNumber: '', // 受理号 6401220002019111600001
    wayOfReceiving: '', // 领取人：0位自己，1位他人
    placeOfCollection: '', // 领取地点
    result: {}

};
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        setCheckResultInfos (state, payload) {
            state.queryIdPhoto = addPrefix(payload.lsXp);
            state.selfCollectionAddress = payload.sfzLzdz;
            state.machineAddress = payload.sfzSldz;

            const isLeft = Object.keys(LEFT_FINGER_POSITION).find((i) => {
                return i === String(payload.zwyZwdm);
            });

            if (isLeft) {
                state.checkResultInfos.leftPos = payload.zwyZwdm || '16';
                state.checkResultInfos.leftPic = payload.zwyZwtxsj;
                state.checkResultInfos.leftQuality = payload.zwyZwtxzlz;
                state.checkResultInfos.leftFeature = payload.zwyZwtzsj;

                state.checkResultInfos.rightPos = payload.zweZwdm || '11';
                state.checkResultInfos.rightPic = payload.zweZwtxsj;
                state.checkResultInfos.rightQuality = payload.zweZwtxzlz;
                state.checkResultInfos.rightFeature = payload.zweZwtzsj;
            } else {
                state.checkResultInfos.leftPos = payload.zweZwdm || '16';
                state.checkResultInfos.leftPic = payload.zweZwtxsj;
                state.checkResultInfos.leftQuality = payload.zweZwtxzlz;
                state.checkResultInfos.leftFeature = payload.zweZwtzsj;

                state.checkResultInfos.rightPos = payload.zwyZwdm || '11';
                state.checkResultInfos.rightPic = payload.zwyZwtxsj;
                state.checkResultInfos.rightQuality = payload.zwyZwtxzlz;
                state.checkResultInfos.rightFeature = payload.zwyZwtzsj;
            }
        },
        setPhoneNumber (state, payload) {
            state.phoneNumber = payload;
        },
        setAcceptanceNumber (state, payload) {
            state.acceptanceNumber = payload;
        },
        setWayOfReceiving (state, payload) {
            state.wayOfReceiving = payload;
        },
        setResult (state, payload) {
            state.result = payload;
        },
        setPlaceOfCollection (state, payload) {
            state.placeOfCollection = payload;
        },
        initData (state) {
            assign(state, cloneDeep(defaultState));
        }
    }
};
