import router from '@/router';
import store from '@/store';
import request from '@/utils/request';
import { completeBusiness } from '@/service/common';

export const changeRouter = (path) => {
    // console.log('跳转路径:', path);
    router.push(path);
};

export const logout = () => {
    console.log('退出到首页');
    completeBusiness();
    store.commit('common/changeVisible', {
        visibleSteps: false,
        visibleTip: false,
        visibleBusinessName: false,
        visibleJurisdiction: false
    });
    store.commit('common/initData'); // 数据清空
    store.commit('permanent/emptyImg'); // 图片清理

    router.push('/');
};

export const getStreamUrl = (id) => {
    return request.getServerUrl() + '/ssm/carMgt/getUserSign?userBusinessId=' + id;
};

export const getPrintPrefix = (id) => {
    return request.getServerUrl() + '/ssm/admin/printTemplate/pdf/' + id;
};
