import { cloneDeep, assign } from 'lodash';

const defaultState = {
    businessItem: {},
    businessDetail: []
};
export default {
    name: 'businessGuide',
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {

        setBusinessItem (state, payload) {
            state.businessItem = payload;
        },

        setBusinessDetail (state, payload) {
            state.businessDetail = payload;
        },

        initData (state) {
            assign(state, cloneDeep(defaultState));
        }
    }
};
