import { BUSINESS_ID } from '@/constant/constant';
import { getBusinessRouters } from '@/router/sub-router';

// 首页的导航数组
const MENUS = [
    {
        must: true,
        icon: require('@/assets/images/menu/menu1.png'),
        background: 'linear-gradient(315deg,rgba(255,86,24,1) 0%,rgba(255,202,41,1) 100%)',
        menu: [],
        name: '常用业务'
    },
    {
        id: BUSINESS_ID.traffic,
        icon: require('@/assets/images/menu/menu3.png'),
        background: 'linear-gradient(315deg,rgba(18,133,255,1) 0%,rgba(32,209,255,1) 100%)',
        menu: [],
        name: '交管业务'
    },
    {
        id: BUSINESS_ID.household,
        icon: require('@/assets/images/menu/menu4.png'),
        background: 'linear-gradient(135deg,rgba(255,168,130,1) 0%,rgba(255,89,128,1) 100%)',
        menu: [],
        name: '户政业务'
    },
    {
        id: BUSINESS_ID.exitEntry,
        icon: require('@/assets/images/menu/menu2.png'),
        background: 'linear-gradient(135deg,rgba(145,107,255,1) 0%,rgba(51,102,255,1) 100%)',
        menu: [],
        name: '出入境业务'
    },
    {
        id: BUSINESS_ID.convenient,
        icon: require('@/assets/images/menu/menu5.png'),
        background: 'linear-gradient(315deg,rgba(146,84,222,1) 0%,rgba(200,149,255,1) 100%)',
        menu: [],
        name: '便民服务'
    },
    {
        id: BUSINESS_ID.businessGuidelines,
        icon: require('@/assets/images/menu/menu5.png'),
        background: 'linear-gradient(135deg,rgba(64,169,255,1) 0%,rgba(9,109,217,1) 100%)',
        menu: [],
        name: '业务指南'
    }
];

export const getMenus = () => {
    const BUSINESS_ROUTERS = getBusinessRouters();

    // 将业务数据添加到导航里
    Object.keys(BUSINESS_ROUTERS).forEach((item) => {
        const data = {
            firId: BUSINESS_ROUTERS[item].firId,
            name: BUSINESS_ROUTERS[item].name,
            icon: BUSINESS_ROUTERS[item].icon,
            code: BUSINESS_ROUTERS[item].code,
            type: BUSINESS_ROUTERS[item].type,
            disable: BUSINESS_ROUTERS[item].disable
        };
        MENUS.forEach((i, index) => {
            if (index === 0) {
                i.menu.push(data);
            }
            if (i.id === BUSINESS_ROUTERS[item].firId) {
                i.menu.push(data);
            }
        });
    });
    return MENUS;
};
