import { cloneDeep, assign } from 'lodash';

const defaultState = {
    usage: [], // 用途
    useType: '',
    printView: '', // 打印的链接
    innocentCompleteRes: {}
};
// 无犯罪记录证明
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        setPrintView (state, payload) {
            state.printView = payload;
        },
        setUsage (state, payload) {
            state.usage = payload.map((item, index) => {
                return {
                    type: Object.keys(item).join(''),
                    value: Object.values(item).join('')
                };
            });
        },
        setUseType (state, payload) {
            state.useType = payload;
        },
        setInnocentCompleteRes (state, payload) {
            state.innocentCompleteRes = payload;
        },
        initData (state) {
            assign(state, cloneDeep(defaultState));
        }
    }
};
