import { cloneDeep, assign } from 'lodash';

// 亲属关系证明
const defaultState = {
    relationRes: {} // 亲属关系证明结果
};
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        setRelationRes (state, payload) {
            state.relationRes = payload;
        },
        initData (state) {
            assign(state, cloneDeep(defaultState));
        }
    }
};
