import { wsSend, wsOnMessage } from '@/utils/websocket';
import QWebChannel from '@/utils/qwebchannel';
import { addPrefix } from '@/utils/tools';
import { getMenuList } from '@/service/common';
import { cloneDeep } from 'lodash';

export default {
    namespaced: true,
    state: {
        originBusinessObject: {},
        machineId: '',
        machineNum: '',
        macAddress: '',
        cardReaderType: '', // T10/CVR
        lightWebSocket: null,
        highPhotoWebSocket: null,
        highPhotoDialog: null,
        highPhotoDev: null,
        highPhotoImg: '',
        singleReflexWebSocket: null,
        singleReflexImgSrc: '',
        singleReflexTakeImgSrc: '',
        identifyWebSocket: null,
        HXWebSocket: null,
        binocularWebSocket: null,
        bankWebSocket: null,
        fingerprintWebSocket: null,
        fingerprintImg: '',
        signWebSocket: null,
        monitorCameraWebSocket: null,
        commonWebSocket: null,
        oneDimensionalCodeWebSocket: null,
        BUSINESS_ROUTERS: {}, // live: {id: BUSINESS_ID.household, code: 101, name: '居住证办理', des: '居住证办理(户政业务)', icon: 'kedaIcon-menu_icon28', component: () => import('@/view/human/live-hefei')},
        BUSINESS_CODE_NAME: [], // 101: "居住证办理(户政业务)"
        INDEX_MENU: {}, // 菜单导航
        BUSINESS_EN_NAME_CODE: {}// driverContact: 301
    },
    mutations: {
        emptyImg (state) {
            state.highPhotoImg = '';
            state.singleReflexImgSrc = '';
            state.singleReflexTakeImgSrc = '';
            state.fingerprintImg = '';
        },
        setINDEX_MENU (state, payload) {
            state.INDEX_MENU = payload;
        },
        setBUSINESS_EN_NAME_CODE (state, payload) {
            const BUSINESS_EN_NAME_CODE = {};
            payload.forEach((item) => {
                BUSINESS_EN_NAME_CODE[item.enName] = item.code;
            });
            state.BUSINESS_EN_NAME_CODE = BUSINESS_EN_NAME_CODE;
        },
        setBUSINESS_CODE_NAME (state, payload) {
            const BUSINESS_CODE_NAME = {};
            payload.forEach((item) => {
                BUSINESS_CODE_NAME[item.code] = item.name;
            });
            state.BUSINESS_CODE_NAME = BUSINESS_CODE_NAME;
        },
        setMachineNum (state, payload) {
            state.machineNum = payload;
        },
        setMachineId (state, payload) {
            state.machineId = payload;
        },
        updateIndexMenu (state, payload) {
            state.originBusinessObject = payload.concat();
            let menus = cloneDeep(state.INDEX_MENU);

            menus.forEach((first, firstIndex) => {
                const firIsExit = payload.find((i) => {
                    return i.businessCode === first.id;
                });
                if (firIsExit) {
                    first.name = firIsExit.businessName;
                    first.disabledBusiness = firIsExit.disabledBusiness;
                }
                if (!firIsExit || (firIsExit && !firIsExit.showFlag)) {
                    menus[firstIndex] = null;
                } else if (first.menu) {
                    first.menu.forEach((second, secondIndex) => {
                        const secIsExit = payload.find((j) => {
                            return j.businessCode === second.id;
                        });
                        if (secIsExit) {
                            second.name = secIsExit.businessName;
                            second.disabledBusiness = secIsExit.disabledBusiness;
                        }
                        if (!secIsExit || (secIsExit && !secIsExit.showFlag)) {
                            menus[firstIndex].menu[secondIndex] = null;
                        } else {
                            second.children.forEach((third, thirdIndex) => {
                                const isExit = payload.find((n) => {
                                    return n.businessCode === third.code;
                                });
                                if (isExit) {
                                    third.name = isExit.businessName;
                                    third.disabledBusiness = isExit.disabledBusiness;
                                }
                                if (!isExit || (isExit && !isExit.showFlag)) {
                                    menus[firstIndex].menu[secondIndex].children[thirdIndex] = null;
                                }
                            });
                        }
                    });
                }
            });

            menus = menus.filter((i) => {
                return i !== null;
            });
            menus.forEach((i) => {
                if (i.menu) {
                    i.menu = i.menu.filter((j) => {
                        return j !== null;
                    });
                }
            });
            menus.forEach((i) => {
                if (i.menu) {
                    i.menu.forEach((j) => {
                        j.children = j.children.filter((z) => {
                            return z !== null;
                        });
                    });
                }
            });

            payload.forEach((item) => {
                state.BUSINESS_CODE_NAME[item.businessCode] = item.businessName;
            });

            state.INDEX_MENU = menus;
        },
        updateIndexMenuN (state, payload) {
            state.originBusinessObject = payload.concat();
            let menus = cloneDeep(state.INDEX_MENU);

            menus = menus.filter((item) => {
                // 过滤常用的，以及大类显示出的导航存在
                return item.must || (payload.find((j) => { return j.parentId === item.id && j.showFlag; }));
            });

            menus.forEach((item) => {
                item.menu = item.menu.filter((i) => {
                    const matchItem = payload.find((j) => {
                        const appearState = j.businessCode === i.code && j.showFlag;
                        return item.must ? appearState && j.commonBusiness : appearState;
                    });
                    if (matchItem) {
                        i.name = matchItem.businessName;
                        i.sortNumber = matchItem.sortNumber;
                        i.disabledBusiness = matchItem.disabledBusiness;
                    }
                    return matchItem;
                });
                item.menu = item.menu.sort(function (a, b) {
                    return a.sortNumber - b.sortNumber;
                });
            });
            menus = menus.filter((item) => {
                if (!item.must) {
                    return item.menu && item.menu.length > 0;
                } else {
                    return true;
                }
            });

            payload.forEach((item) => {
                state.BUSINESS_CODE_NAME[item.businessCode] = item.businessName;
            });

            state.INDEX_MENU = menus;
        },
        setLightWebSocket (state, payload) {
            const connect = () => {
                const socket = new WebSocket('ws://localhost:9091/lightctrl');
                socket.onerror = (event) => {
                    console.warn('灯光控制不可用');
                };
                // 连接成功建立的回调方法
                socket.onopen = (event) => {
                    state.lightWebSocket = socket;
                    console.log('灯光控制连接成功');
                    wsSend(socket, 'Ytj');
                    wsSend(socket, 'lightCtrl@C1:ON;C2:ON');
                };
                wsOnMessage(socket, (res) => {
                    console.log('灯光控制收到消息', res);
                    if (res && res.code === 1) {
                        if (res.module === 'Ytj') {
                            if (window.location.hostname !== 'localhost') {
                                state.macAddress = res.result.deviceId;
                            }
                            state.cardReaderType = res.result.cardReaderType;
                        }
                    }
                });
            };
            connect();
        },
        setHighPhotoDialog (state, payload) {
            const connect = () => {
                const socket = new WebSocket('ws://localhost:12345');
                socket.onerror = (event) => {
                    console.warn('高拍仪不可用');
                };
                socket.onopen = () => {
                    state.highPhotoWebSocket = socket;
                    console.log('高拍仪连接成功');
                    /* eslint-disable no-new */
                    new QWebChannel(socket, (channel) => {
                        // 初始化高拍仪
                        if (channel && channel.objects && channel.objects.dialog) {
                            state.highPhotoDialog = channel.objects.dialog;
                            channel.objects.dialog.sendPrintInfo.connect((message) => {
                                console.log('高拍仪收到服务器消息', message);
                                if (message.indexOf('priDevName:') >= 0) {
                                    state.highPhotoDev = message.substr(11);
                                }
                            });
                            channel.objects.dialog.send_priImgData.connect((message) => {
                                state.highPhotoImg = addPrefix(message);
                            });
                        }
                    });
                };
            };
            connect();
        },
        setSingleReflexWebSocket (state, payload) {
            const connect = () => {
                const socket = new WebSocket('ws://localhost:9091/cameractrl');
                socket.onopen = () => {
                    state.singleReflexWebSocket = socket;
                    console.log('单反连接成功');
                };
                wsOnMessage(socket, (res) => {
                    if (res && res.code === 1) {
                        if (res.directive === 'Preview') {
                            state.singleReflexImgSrc = addPrefix(res.result);
                        } else if (res.directive === 'TakePhoto') {
                            state.singleReflexTakeImgSrc = addPrefix(res.result);
                        }
                    }
                });
                socket.onerror = (event) => {
                    console.warn('单反不可用');
                };
            };
            connect();
        },
        setSingleReflexImgSrc (state, payload) {
            state.singleReflexImgSrc = payload;
        },
        setSingleReflexTakeImgSrc (state, payload) {
            state.singleReflexTakeImgSrc = payload;
        },
        setIdentifyWebSocket (state, payload) {
            let url = '';
            if (state.cardReaderType === 'CVR') { // 老读卡器
                url = 'ws://localhost:9091/cardreader';
            } else if (state.cardReaderType === 'T10') { // 多合一读卡器
                url = 'ws://localhost:9091/t10ctrl';
            }

            const connect = () => {
                const socket = new WebSocket(url);
                socket.onerror = (event) => {
                    console.warn('读卡器不可用');
                };
                socket.onopen = () => {
                    state.identifyWebSocket = socket;
                    console.log('读卡器连接成功');
                };
            };
            connect();
        },
        setHXWebSocket (state, payload) {
            const connect = () => {
                const socket = new WebSocket('ws://localhost:9091/hangxinctrl');
                socket.onopen = () => {
                    state.HXWebSocket = socket;
                    console.log('航信签注机连接成功');
                };
                socket.onerror = (event) => {
                    console.warn('签注机不可用');
                };
            };
            connect();
        },
        setBinocularWebSocket (state, payload) {
            const connect = () => {
                const socket = new WebSocket('ws://localhost:9091/yituctrl');
                socket.onopen = () => {
                    state.binocularWebSocket = socket;
                    console.log('双目摄像头连接成功');
                };
                socket.onerror = (event) => {
                    console.warn('双目摄像头不可用');
                };
            };
            connect();
        },
        setBankWebSocket (state, payload) {
            const connect = () => {
                const socket = new WebSocket('ws://localhost:9091/bankposctrl');
                socket.onerror = (event) => {
                    console.warn('银行硬件不可用');
                };
                // 连接成功建立的回调方法
                socket.onopen = (event) => {
                    state.bankWebSocket = socket;
                    console.log('银行硬件连接成功');
                };
            };
            connect();
        },
        setFingerprintWebSocket (state, payload) {
            const connect = () => {
                const socket = new WebSocket('ws://localhost:9091/fingerctrl');
                socket.onopen = () => {
                    state.fingerprintWebSocket = socket;
                    console.log('指纹连接成功');
                };
                socket.onerror = (event) => {
                    console.warn('指纹不可用');
                };
            };
            connect();
        },
        setMonitorCameraWebSocket (state, payload) {
            const connect = () => {
                const socket = new WebSocket('ws://localhost:9091/monitorCamera');
                socket.onopen = () => {
                    state.monitorCameraWebSocket = socket;
                    console.log('监控连接成功');
                };
                socket.onerror = (event) => {
                    console.warn('监控不可用');
                };
            };
            connect();
        },
        setSignWebSocket (state, payload) {
            const connect = () => {
                const socket = new WebSocket('ws://localhost:9091/hwWriterCmd');
                socket.onopen = () => {
                    state.signWebSocket = socket;
                    console.log('签字版连接成功');
                };
                socket.onerror = (event) => {
                    console.warn('签字版不可用');
                };
            };
            connect();
        },
        setOneDimensionalCodeWebSocket (state, payload) {
            const connect = () => {
                const socket = new WebSocket('ws://localhost:9091/scancodectrl');
                socket.onopen = () => {
                    state.oneDimensionalCodeWebSocket = socket;
                    console.log('一维码扫描仪连接成功');
                };
                socket.onerror = (event) => {
                    console.warn('一维码扫描仪不可用');
                };
            };
            connect();
        },
        setCommonSocket (state, payload) {
            const connect = () => {
                const socket = new WebSocket('ws://localhost:9091/commonctrl');
                socket.onopen = () => {
                    state.commonWebSocket = socket;
                    console.log('工具类连接成功');
                    wsSend(socket, { directive: 'macAddress' });
                };
                socket.onerror = (event) => {
                    console.warn('工具类不可用');
                };
                wsOnMessage(socket, (res) => {
                    console.log('工具类收到消息', res);
                    if (res && res.code === 1) {
                        if (res.module === 'Ytj') {
                            if (window.location.hostname !== 'localhost') {
                                state.macAddress = res.result.deviceId;
                            }
                            state.cardReaderType = res.result.cardReaderType;
                        } else if (res.module === 'Chrome') {
                            if (res.result === 'refresh') {
                                window.location.href = window.location.origin + window.location.pathname;
                            }
                        }
                    }
                });
            };
            connect();
        },
        upDataMac (state, payload) {
            const address = localStorage.getItem('macAddress');
            if (address) {
                state.macAddress = address; // todo 本地测试时候加入mac地址用
            }
        },
        initData (state, payload) {
        }
    },
    actions: {
        // 老的导航
        async getMenuList ({ state, commit }) {
            const res = await getMenuList({
                deviceId: state.macAddress
            });
            if (res && res.code === '0') {
                commit('updateIndexMenu', res.result.ytjBusinesses);
                commit('setMachineNum', res.result.serialNumber);
                commit('setMachineId', res.result.ytjId);
            }
        },
        async getMenuListText ({ state, commit }, payload) {
            // todo 老的导航测试demo用
            commit('updateIndexMenu', payload.result.ytjBusinesses);
            commit('setMachineNum', payload.result.serialNumber);
            commit('setMachineId', payload.result.ytjId);
        },
        // 新的导航
        async getMenuListN ({ state, commit }) {
            const res = await getMenuList({
                deviceId: state.macAddress
            });
            if (res && res.code === '0') {
                commit('updateIndexMenuN', res.result.ytjBusinesses);
                commit('setMachineNum', res.result.serialNumber);
                commit('setMachineId', res.result.ytjId);
            }
        },
        async getMenuListTextN ({ state, commit }, payload) {
            // todo 新的导航测试demo用
            commit('updateIndexMenuN', payload.result.ytjBusinesses);
            commit('setMachineNum', payload.result.serialNumber);
            commit('setMachineId', payload.result.ytjId);
        },
        connectWebSocket ({ state, commit }, payload) {
            commit('upDataMac', payload);// todo 本地测试时候加入mac地址用
            if (!payload.disableHardwareConnect) {
                commit('setLightWebSocket', payload);
                commit('setHighPhotoDialog');
                commit('setSingleReflexWebSocket');
                commit('setHXWebSocket');
                commit('setBinocularWebSocket');
                commit('setBankWebSocket');
                commit('setFingerprintWebSocket');
                commit('setSignWebSocket');
                commit('setMonitorCameraWebSocket');
                commit('setCommonSocket');
                commit('setOneDimensionalCodeWebSocket');

                const intervalObj = setInterval(() => {
                    if (state.cardReaderType) {
                        console.log('查询到读卡器类型:', state.cardReaderType);
                        clearInterval(intervalObj);
                        commit('setIdentifyWebSocket');
                    }
                }, 1000);
            }
        },
        startMonitorCamera ({ state, commit }, payload) {
            wsSend(state.monitorCameraWebSocket, { directive: 'Start', obj: payload });
        },
        stopMonitorCamera ({ state, commit }, payload) {
            wsSend(state.monitorCameraWebSocket, { directive: 'Stop' });
        },
        stopAllWs (state, payload) {
            wsSend(state.lightWebSocket, 'lightCtrl@C1:OFF;C2:OFF;B3:OFF;B4:OFF;B5:OFF;B6:OFF');
            wsSend(state.singleReflexWebSocket, { module: 'Camera', directive: 'stop' });
            wsSend(state.signWebSocket, { module: 'HwWriter', directive: 'Stop' });
            wsSend(state.fingerprintWebSocket, { module: 'Finger', directive: 'Stop' });
            wsSend(state.monitorCameraWebSocket, { directive: 'Stop' });
            state.highPhotoDialog.get_actionType('closeSignal');
        }
    }
};
