import { cloneDeep, assign } from 'lodash';
import { LEFT_FINGER_POSITION } from '@/constant/constant';

// 身份证期满换证

const defaultState = {
    acceptanceNumber: '',
    previewPrintPhoto: '',
    handleType: 1, // 1自取 2邮寄
    cardPostInfo: {
        postName: '',
        recipientsPhone: '',
        mailingAddress: ''
    },
    payQR: '',
    payMoney: 0,
    originFingerInfo: {
        zwyTxsj: '', // 指纹一图像数据
        zwyTzsj: '', // 指纹一特征数据
        zwyzw: '', // 指纹一指位
        zweTxsj: '', // 指纹二图像数据
        zweTzsj: '', // 指纹二特征数据
        zwezw: '' // 指纹二指位
    },
    fingerInfo: {},
    uploadFingerInfo: {},
    collectAddresses: '',
    uploadResult: {
        slh: '', // 受理号
        cbr: '', // 承办人
        sldw: ''// 受理单位
    },
    orderDetail: {}
};
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        setFingerInfo (state, payload) {
            state.originFingerInfo = JSON.parse(JSON.stringify(payload));

            const isLeft = Object.keys(LEFT_FINGER_POSITION).find((i) => {
                return i === String(payload.zwyzw);
            });

            if (isLeft) {
                state.fingerInfo.leftPos = payload.zwyzw || '16';
                state.fingerInfo.leftPic = payload.zwyTxsj;
                state.fingerInfo.leftFeature = payload.zwyTzsj;
                state.fingerInfo.leftQuality = payload.zwyTxzlz;

                state.fingerInfo.rightPos = payload.zwezw || '11';
                state.fingerInfo.rightPic = payload.zweTxsj;
                state.fingerInfo.rightFeature = payload.zweTzsj;
                state.fingerInfo.rightQuality = payload.zweTxzlz;
            } else {
                state.fingerInfo.leftPos = payload.zwezw || '16';
                state.fingerInfo.leftPic = payload.zweTxsj;
                state.fingerInfo.leftFeature = payload.zweTzsj;
                state.fingerInfo.leftQuality = payload.zweTxzlz;

                state.fingerInfo.rightPos = payload.zwyzw || '11';
                state.fingerInfo.rightPic = payload.zwyTxsj;
                state.fingerInfo.rightFeature = payload.zwyTzsj;
                state.fingerInfo.rightQuality = payload.zwyTxzlz;
            }
        },
        setUploadFingerInfo (state, payload) {
            if (payload.constant.disableFingerContrast) {
                state.uploadFingerInfo.zwyimage = state.originFingerInfo.zwyTxsj;
                state.uploadFingerInfo.zwyfeature = state.originFingerInfo.zwyTzsj;
                state.uploadFingerInfo.zwyZwtxzlz = state.originFingerInfo.zwyTxzlz;

                state.uploadFingerInfo.zweimage = state.originFingerInfo.zweTxsj;
                state.uploadFingerInfo.zwefeature = state.originFingerInfo.zweTzsj;
                state.uploadFingerInfo.zweZwtxzlz = state.originFingerInfo.zweTxzlz;
            } else {
                // todo 测试身份证指纹
                // state.uploadFingerInfo.zwyimage = payload.constant.zwyimage;
                // state.uploadFingerInfo.zwyfeature = payload.constant.zwyfeature;
                // state.uploadFingerInfo.zwyZwtxzlz = payload.constant.zwyZwtxzlz;
                //
                // state.uploadFingerInfo.zweimage = payload.constant.zweimage;
                // state.uploadFingerInfo.zwefeature = payload.constant.zwefeature;
                // state.uploadFingerInfo.zweZwtxzlz = payload.constant.zweZwtxzlz;

                const isLeft = Object.keys(LEFT_FINGER_POSITION).find((i) => {
                    return i === String(state.originFingerInfo.zwyzw);
                });

                if (isLeft) {
                    state.uploadFingerInfo.zwyimage = payload.left.base64;
                    state.uploadFingerInfo.zwyfeature = payload.left.feature;
                    state.uploadFingerInfo.zwyZwtxzlz = payload.left.quantity;

                    state.uploadFingerInfo.zweimage = payload.right.base64;
                    state.uploadFingerInfo.zwefeature = payload.right.feature;
                    state.uploadFingerInfo.zweZwtxzlz = payload.right.quantity;
                } else {
                    state.uploadFingerInfo.zwyimage = payload.right.base64;
                    state.uploadFingerInfo.zwyfeature = payload.right.feature;
                    state.uploadFingerInfo.zwyZwtxzlz = payload.right.quantity;

                    state.uploadFingerInfo.zweimage = payload.left.base64;
                    state.uploadFingerInfo.zwefeature = payload.left.feature;
                    state.uploadFingerInfo.zweZwtxzlz = payload.left.quantity;
                }
            }
        },
        setAcceptanceNumber (state, payload) {
            state.acceptanceNumber = payload;
        },
        setUploadResult (state, payload) {
            state.uploadResult = payload;
        },
        setCollectAddresses (state, payload) {
            state.collectAddresses = payload;
        },
        setPayInfo (state, payload) {
            state.payQR = payload.payQR;
            state.payMoney = payload.payMoney;
        },
        setPreviewPrintPhoto (state, payload) {
            state.previewPrintPhoto = payload;
        },
        setHandleType (state, payload) {
            state.handleType = payload;
        },
        setCardPostInfo (state, payload) {
            state.cardPostInfo = { ...payload };
        },
        setOrderDetail (state, payload) {
            state.orderDetail = payload;
        },
        initData (state) {
            assign(state, cloneDeep(defaultState));
        }
    }
};
