import { cloneDeep, assign } from 'lodash';
import { translateLicense } from '../../../../utils/tools';

const defaultState = {
    driverLicenseInstructions: '',
    driverLicenseUserInfo: {},
    driverLicenseBusinessInfo: {},

    driverLicenseInfo: {},
    payQrUrl: '',
    driverLicensePhoto: ''
};
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        setDriverLicenseInfo (state, payload) {
            state.driverLicenseInfo = payload;
        },
        setPayQrUrl (state, payload) {
            state.payQrUrl = payload;
        },
        setDriverLicensePhoto (state, payload) {
            state.driverLicensePhoto = payload;
        },
        setDriverLicenseInstructions (state, payload) {
            state.driverLicenseInstructions = payload;
        },
        setDriverLicenseUserInfo (state, payload) {
            state.driverLicenseUserInfo = translateLicense(payload);
        },
        setDriverLicenseBusinessInfo (state, payload) {
            state.driverLicenseBusinessInfo = payload;
        },
        initData (state) {
            assign(state, cloneDeep(defaultState));
        }
    }
};
