export const excelPrint = (params) => {
    // A4打印excel
    if (window.options.demo) {
        return new Promise(resolve => resolve({ code: 1, message: '成功', result: null }));
    }
    return new Promise(async (resolve) => {
        const result = await request.ajax('http://localhost:9094/excelPrint', 'post', params, { isLocalHttp: true });
        setTimeout(() => {
            resolve(result);
        }, 15000);
    });
};

