// 刷完身份证判断能否继续办理业务
export const isHandle = (params) => {
    const traffic = BUSINESS_ID.vehicleSec;
    const human = BUSINESS_ID.householdSec;

    if (params.businessType > traffic && params.businessType < (traffic + 100)) {
        return request.ajax('/ssm/commonapi/precheck', 'post', params);
    } else if (params.businessType > human && params.businessType < (human + 100)) {
        return request.ajax('/ssm/commonzwapi/precheck', 'post', params);
    } else {
        return { code: '0' };
    }
};

