import { cloneDeep, assign } from 'lodash';
import moment from 'moment';

const defaultState = {
    illegalityPayIntroduce: '',
    illegalityPayType: 0,
    illegalityPayList: [],
    illegalityPayResult: {},

    illegalityElectronicIntroduce: '',
    illegalityElectronicMonitorList: [],
    illegalityElectronicSelectMonitor: { recordArr: [] },
    illegalityPayUrl: { smURL: '' },
    illegalityElectronicResult: {},

    illegalityPayInfo: [],
    illegalityResult: {},

    driverInfo: {}, // 驾驶证信息
    carInfo: {}, // 机动车信息
    scenePhotos: '', // 现场采集照片
    carLicensePhoto: '', // 现场采集驾驶证照片
    driverLicensePhoto: '', // 现场采集机动车照片

    handleCarInfo: {}, // 处理人的车辆信息
    carLists: [{}], // 同户下的所有车辆集合
    ownCarLists: [] // 自己的车/同户的他人车
};
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        setIllegalityPayIntroduce (state, payload) {
            state.illegalityPayIntroduce = payload;
        },
        setIllegalityPayType (state, payload) {
            state.illegalityPayType = payload;
        },
        setIllegalityPayList (state, payload) {
            payload.forEach((item, index) => {
                if (!item.lateFee) {
                    item.lateFee = 0;
                }
                if (!item.payAmount) {
                    item.payAmount = 0;
                }
            });
            state.illegalityPayList = payload;
        },
        setIllegalityElectronicIntroduce (state, payload) {
            state.illegalityElectronicIntroduce = payload;
        },
        setIllegalityElectronicMonitorList (state, list, stateList) {
            state.illegalityElectronicMonitorList = list;
        },
        setIllegalityElectronicSelectMonitor (state, payload) {
            const list = cloneDeep(payload.list);

            delete payload.list.monitorProcessingRecordDTOS;
            delete payload.list.monitorRecordIds;
            delete payload.list.status;

            state.illegalityElectronicSelectMonitor = {
                holder: payload.list.holder,
                mobile: payload.list.mobile,
                licenceNo: payload.list.licenceNo,
                plateNumber: payload.list.plateNumber,
                recordArr: list.monitorProcessingRecordDTOS.map((item) => {
                    const dataMatch = payload.selection.find((i) => { return i.jkbh === item.jkbh; });
                    if (dataMatch) {
                        return { ...dataMatch, ...item, ...payload.list };
                    } else {
                        return { ...item, ...payload.list };
                    }
                }),
                totalFines: payload.totalFines,
                totalScore: payload.totalScore
            };
        },
        updateSelectMonitor (state, payload) {
            let recordArr = cloneDeep(state.illegalityElectronicSelectMonitor.recordArr);
            // 更新决定书编号
            recordArr = recordArr.map((item) => {
                const i = payload.find((x) => {
                    return x.monitorRecordId === item.monitorRecordId;
                });

                if (i) {
                    const obj = { ...item, ...i, ...i.monitorProcessingRecordDTO };
                    obj.illegalTimeFormat = moment(obj.illegalTime).format('YYYY年MM月DD日HH时mm分');
                    obj.year = moment().format('YYYY');
                    obj.month = moment().format('MM');
                    obj.day = moment().format('DD');
                    delete obj.monitorProcessingRecordDTO;
                    return obj;
                }
            });
            state.illegalityElectronicSelectMonitor.recordArr = recordArr;
        },
        setIllegalityPayUrl (state, payload) {
            state.illegalityPayUrl = payload;
        },
        setIllegalityResult (state, payload) {
            state.illegalityResult = payload;
        },
        setIllegalityPayInfo (state, payload) {
            state.illegalityPayInfo = payload;
        },
        setIllegalityPayResult (state, payload) {
            state.illegalityPayResult = payload;
        },
        setCarLists (state, payload) {
            state.carLists = payload;
        },
        setOwnCarLists (state, payload) {
            state.ownCarLists = payload;
        },
        setHandleCarInfo (state, payload) {
            state.handleCarInfo = payload;
        },
        setDriverInfo (state, payload) {
            state.driverInfo = payload;
        },
        setCarInfo (state, payload) {
            state.carInfo = payload;
        },
        saveScenePhotos (state, payload) {
            state.scenePhotos = payload;
        },
        saveCarLicensePhoto (state, payload) {
            state.carLicensePhoto = payload;
        },
        saveDriverLicensePhoto (state, payload) {
            state.driverLicensePhoto = payload;
        },
        initData (state) {
            assign(state, cloneDeep(defaultState));
        }

    }
};
