import Vue from 'vue';
import App from './App.vue';
import store from './store';
import router from './router';
import axios from 'axios';

import './plugins/element.js';
import 'normalize.css';

import '@/utils/logs';
import '@/assets/baseUI/base-style.css';
import '@/assets/baseUI/base-fonticon/iconfont.css';
import '@/assets/css/common.css';

import './vuePrototype';

import request from '@/utils/request';

Vue.config.productionTip = false;

// 禁用后退前进功能
history.pushState(null, null, document.URL);
window.addEventListener('popstate', () => {
    history.pushState(null, null, document.URL);
});

// 禁止双指放大
document.documentElement.addEventListener('touchstart', function (event) {
    if (event.touches.length > 1) {
        event.preventDefault();
    }
}, false);

// 禁止双击放大
let lastTouchEnd = 0;
document.documentElement.addEventListener('touchend', function (event) {
    const now = Date.now();
    if (now - lastTouchEnd <= 300) {
        event.preventDefault();
    }
    lastTouchEnd = now;
}, false);

axios.get('config/' + window.buildType + 'Global.json').then((res) => {
    document.title = res.data.documentTitle;
    request.initConfig(res.data);
    // initOptions(res.data);
    window.options = res.data;
    Vue.prototype.CONSTANT = { ...res.data };

    new Vue({
        router,
        store,
        render: h => h(App)
    }).$mount('#app');
});
