import moment from 'moment';
import { CHINESS_NAME_PY, CHINESS_NAME_TIME_PY } from '@/constant/constant';
import request from '@/utils/request';

// 图像转灰度
export const gray = (imgSrc) => {
    return new Promise(resolve => {
        const imgObj = document.createElement('img');
        imgObj.crossOrigin = 'anonymous';
        imgObj.onload = () => {
            const canvas = document.createElement('canvas');
            const canvasContext = canvas.getContext('2d');
            const imgW = imgObj.width;
            const imgH = imgObj.height;
            canvas.width = imgW;
            canvas.height = imgH;

            canvasContext.drawImage(imgObj, 0, 0);
            const imgPixels = canvasContext.getImageData(0, 0, imgW, imgH);

            for (let y = 0; y < imgPixels.height; y++) {
                for (let x = 0; x < imgPixels.width; x++) {
                    const i = (y * 4) * imgPixels.width + x * 4;
                    const avg = (imgPixels.data[i] + imgPixels.data[i + 1] + imgPixels.data[i + 2]) / 3;
                    imgPixels.data[i] = avg;
                    imgPixels.data[i + 1] = avg;
                    imgPixels.data[i + 2] = avg;
                }
            }
            canvasContext.putImageData(imgPixels, 0, 0, 0, 0, imgPixels.width, imgPixels.height);
            resolve(canvas.toDataURL());
        };
        imgObj.src = imgSrc;
    });
};

export const cancelPrefix = (base64) => {
    if (base64 && typeof base64 === 'string' && base64.match('data:image')) {
        return base64.split(',')[1];
    } else {
        return base64;
    }
};

export const addPrefix = (base64) => {
    if (base64 && typeof base64 === 'string' && !base64.match('data:image')) {
        return 'data:image/png;base64,' + base64;
    } else {
        return base64;
    }
};

export const momentFormat = (time) => {
    return time ? moment(time).format('YYYY年MM月DD日') : '';
};

export const translateLicense = (val) => { // val : {}
    const data = [];
    Object.keys(val).forEach((item) => {
        if (CHINESS_NAME_PY[item]) {
            data.push({
                label: CHINESS_NAME_PY[item],
                value: val[item]
            });
        }
    });
    Object.keys(val).forEach((item) => {
        if (CHINESS_NAME_TIME_PY[item]) {
            const newDataFormat = momentFormat(val[item]);
            data.push({
                label: CHINESS_NAME_TIME_PY[item],
                value: newDataFormat === 'Invalid date' ? val[item] : newDataFormat
            });
        }
    });
    return data;
};

// url匹配
export const matchUrl = (url, addUrl) => {
    if (!url.match('http://') && !url.match('https://')) {
        return addUrl + url;
    } else {
        return url;
    }
};

// 检验身份证正则
export const checkIdNum = (data) => {
    const reg = /^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/;
    return reg.test(data);
};

// 检验姓名正则
export const checkName = (data) => {
    const reg = /^([\u4e00-\u9fa5\]{1,20}|[a-zA-Z\s]{1,20})$/;
    return reg.test(data);
};

// 检验手机正则
export const checkPhone = (data) => {
    const reg = /^\d+$|^\d+[.]?\d+$/;
    return reg.test(data) && data.length === 11;
};

// 不同读卡器的数据兼容处理
export const cardReadData = (data, type) => {
    let newData = {};
    if (type === 'Handwriting') { // 手写输入情况
        newData = {
            gender: Number(data.identifyNum[16] % 2) === 0 ? 2 : 1,
            certNumber: data.identifyNum,
            name: data.identifyName,
            partyName: data.identifyName
        };
    } else if (type === 'CVR') { // CVR读卡器
        newData = {
            ...data,
            identifyCardNumber: data.certNumber,
            name: data.partyName,
            gender: Number(data.certNumber[16] % 2) === 0 ? 2 : 1
        };
    } else if (type === 'T10') { // T10读卡器
        newData = {
            certOrg: data.Gov,
            bornDay: data.Birth,
            certAddress: data.Address,
            certNumber: data.IdNum,
            identifyCardNumber: data.IdNum,
            name: data.Name,
            partyName: data.Name,
            nation: data.Nation,
            expDate: data.EndDate,
            effDate: data.BeginDate,
            identityPic: data.Photo,
            // gender: data.Gender === '男' ? 1 : 2
            gender: Number(data.IdNum[16] % 2) === 0 ? 2 : 1
        };
    }
    return newData;
};

export const compressCanvas = (canvas, width, height) => {
    const quality = 0.6; // 压缩系数0-1之间

    const ctx = canvas.getContext('2d');
    if (width) {
        canvas.width = width;
    }
    if (height) {
        canvas.height = height;
    }
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    ctx.drawImage(this, 0, 0, canvas.width, canvas.height);
    return canvas.toDataURL('image/jpeg', quality);
};

export const compressBase64 = (base64, w, callback) => {
    const newImage = new Image();
    let quality = 0.6; // 压缩系数0-1之间
    newImage.src = base64;
    newImage.setAttribute('crossOrigin', 'Anonymous');	// url为外域时需要
    let imgWidth, imgHeight;
    newImage.onload = function () {
        imgWidth = this.width;
        imgHeight = this.height;
        const canvas = document.createElement('canvas');
        const ctx = canvas.getContext('2d');
        if (Math.max(imgWidth, imgHeight) > w) {
            if (imgWidth > imgHeight) {
                canvas.width = w;
                canvas.height = w * imgHeight / imgWidth;
            } else {
                canvas.height = w;
                canvas.width = w * imgWidth / imgHeight;
            }
        } else {
            canvas.width = imgWidth;
            canvas.height = imgHeight;
            quality = 0.6;
        }
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        ctx.drawImage(this, 0, 0, canvas.width, canvas.height);
        const base64 = canvas.toDataURL('image/jpeg', quality); // 压缩语句
        // 如想确保图片压缩到自己想要的尺寸,如要求在50-150kb之间，请加以下语句，quality初始值根据情况自定
        // while (base64.length / 1024 > 150) {
        // 	quality -= 0.01;
        // 	base64 = canvas.toDataURL("image/jpeg", quality);
        // }
        // 防止最后一次压缩低于最低尺寸，只要quality递减合理，无需考虑
        // while (base64.length / 1024 < 50) {
        // 	quality += 0.001;
        // 	base64 = canvas.toDataURL("image/jpeg", quality);
        // }
        callback(base64);// 必须通过回调函数返回，否则无法及时拿到该值
    };
};

export const getYearMouthOfDay = (year, mouth) => {
    const time = new Date(year, mouth);
    time.setDate(0);
    return time.getDate();
};

export const urlGetQrEncode = (url) => {
    const finalUrl = request.getServerUrl() + '/ssm/admin/printTemplate/qrcode?content=' + encodeURIComponent(url);
    return finalUrl;
};
